import React, { useState, useEffect } from 'react'
import { Button, Dropdown, OverlayTrigger, Tooltip } from 'react-bootstrap'
import ReactHtmlParser from 'react-html-parser'
import { getFeedlist } from '../../../../../../actions/appsMoreAction'
import { COPY_TO_CLIPBOARD, GET_NETWORK_ICON, GET_NETWORK_NAME } from '../../../../../../utils'
import { baseUrl, FontAwesomeIcon } from '../../../../../../Assets/AssetsHelper'
import { DOMAIN_NAME, DOMAIN_NAME_HOST, IS_API_ACCESS_LIVE, IS_TAGEMBED } from '../../../../../../actions/api'
import { getGoogleSchema } from '../../../../../../actions/publishAction'
import ShareCodeModal from '../../../CommonComponents/ShareCodeModal'
import EmptyWithIcon from '../../../CommonComponents/Empty/EmptyWithIcon'
import { useNavigate } from 'react-router-dom'

export default function RatingSteps({ wallData }) {

    const [feedList, setFeedList] = useState([])
    const [selectedFeed, setSelectedFeed] = useState([])
    const [copied, setCopied] = useState(false)
    const [codeRequest, setCodeRequest] = useState(false)
    const [schema, setSchema] = useState('')
    const [mailCode, setMailCode] = useState('')
    const [openPopup, setOpenPopup] = useState(false)
    const [feedRequest, setFeedRequest] = useState(false)
    const [loader, setLoader] = useState(false)
    const navigate = useNavigate()


    useEffect(() => {
        setFeedRequest(true)
        setCodeRequest(true)
        getFeedlist(wallData?.ThemeInfo?.wallId || localStorage.getItem('active_wall')).then(response => {
            setFeedRequest(false)
            const { responsedata } = response.data
            const list = responsedata?.feeds?.filter(item => item?.isReview == 1) || []
            if (!list?.length) setLoader(true)
            setFeedList(list)
            setSelectedFeed(list)
        }).catch(() => {
            setFeedRequest(false)
            setCodeRequest(false)
        })
    }, [])

    useEffect(() => {
        if (selectedFeed?.length) getSchema()
    }, [selectedFeed])

    const onSelectFeed = (feed) => {
        if (selectedFeed.includes(feed)) {
            setSelectedFeed(selectedFeed.filter(feeds => feeds !== feed))
        } else {
            setSelectedFeed([...selectedFeed, feed])
        }
    }

    const getSchema = () => {
        if (selectedFeed?.length) {
            const wallId = selectedFeed[0]?.wallEncrytedId
            const feeds = selectedFeed.map(feed => feed.feedEncrytedId).join(',')
            setCodeRequest(true)
            getGoogleSchema(wallId, feeds).then(response => {
                const { data, response_code } = response.data
                setCodeRequest(false);
                setSchema(response_code === 200 ?
                    `<script type="application/ld+json" class="${DOMAIN_NAME}-schema-code" data-ref="${wallId}" data-feed-ref="${feeds}" > { "@context": "https://schema.org", "@type": "Product", "name": "${data?.name}", "aggregateRating": { "@type": "AggregateRating", "ratingValue": ${data?.rating?.rating}, "reviewCount": ${data?.rating?.count} } } </script> <script src="https://widget${IS_API_ACCESS_LIVE ? '' : IS_TAGEMBED ? '-test' : '-lite'}${DOMAIN_NAME_HOST}/js/schema.js"></script>` : ""
                );
                setMailCode(`<pre style='background-color:#333; color:#fff; padding:10px; border-radius:5px; font-family:monospace; overflow-x:auto; white-space:pre-wrap;'><code>&lt;script type=\"application/ld+json\" class=\"${DOMAIN_NAME}-schema-code\" data-ref=\"${wallId}\" data-feed-ref=\"${feeds}\"&gt; { \"@context\": \"https://schema.org\", \"@type\": \"Product\", \"name\": \"${data?.name}\", \"aggregateRating\": { \"@type\": \"AggregateRating\", \"ratingValue\": ${data?.rating?.rating}, \"reviewCount\": ${data?.rating?.count} } } &lt;/script&gt;\n&lt;script src=\"https://widget${IS_API_ACCESS_LIVE ? '' : IS_TAGEMBED ? '-test' : '-lite'}${DOMAIN_NAME_HOST}/js/schema.js\"&gt;&lt;/script&gt;</code></pre>`)
            }).catch(() => {
                setSchema("");
                setMailCode("");
                setCodeRequest(false);
            })
        }
    }

    const selectAllFeeds = (event) => {
        setSelectedFeed(event.target.checked ? feedList : [])
    }

    const copyText = (id) => {
        COPY_TO_CLIPBOARD(id)
        setCopied(true)
        setTimeout(() => { setCopied(false) }, 1000)
    }

    const addFeed = (id) => {
        localStorage.setItem('wallId', id)
        navigate(`/content/addfeed/${id}`)
    }
    const pageContent = window?.APP_CONTENT ?? {};
    const { galleryTypeObj = {} } = pageContent?.publish;
    const { commonContent, rattingSchema, modal, shopifyPublish } = galleryTypeObj //No Feeds Available
    const { ratingEmptyIcon } = modal
     
    return (
        <div className='mb-10 pb-10'>
            <h5 className="mb-5">{commonContent?.heading ?? ""}</h5>

            {!loader ?
                <>
                    <p className="h5 mb-2 mt-6">{rattingSchema?.selectFeed ?? ""}</p>
                    {!feedRequest ? <Dropdown className="w-100" autoClose="outside">
                        <Dropdown.Toggle variant="outline-secondary" className="border-gray w-100 d-flex align-items-center">
                            <label className="form-check flex-grow-1 overflow-hidden align-items-center" htmlFor={`filter_${selectedFeed && selectedFeed.length > 0 ? GET_NETWORK_NAME(selectedFeed[0].networkId) : ""}`}>
                                <span className="form-check-label text-gray-800 text-ellipsis fw-medium ps-0">
                                    {selectedFeed.length > 0
                                        ? selectedFeed.length === 1
                                            ? ReactHtmlParser(selectedFeed[0].name)
                                            : `${selectedFeed.length} selected`
                                        : rattingSchema?.selectfeed ?? ""}
                                </span>
                            </label>
                        </Dropdown.Toggle>
                        <Dropdown.Menu className="w-100 overflow-auto" style={{ maxHeight: 400 }}>
                            <div className="d-flex align-items-center px-3 cursor-pointer mb-2">
                                <label className="form-check flex-grow-1 fw-normal fs-8" htmlFor="tri-searchFeeds">
                                    { pageContent?.common?.lbl_select_all ?? ""}
                                </label>
                                <input
                                    className="form-check-input"
                                    type="checkbox"
                                    name=""
                                    value=""
                                    id="tri-searchFeeds"
                                    onChange={event => selectAllFeeds(event)}
                                    checked={feedList.length === selectedFeed.length && feedList.length !== 0}
                                />
                            </div>
                            {feedList && feedList.length > 0 ? feedList.map(feed => (
                                <div className='dropdown-item' key={feed.feedId}>
                                    <label className="form-check flex-grow-1 overflow-hidden align-items-center" htmlFor={`tri-conn-${feed.feedId}`}>
                                        <span className="symbol symbol-20">
                                            <img
                                                className="img-fluid h-100"
                                                src={baseUrl(`media/icons/social-icons-square/${GET_NETWORK_ICON(feed.networkId)}.svg`)}
                                                height={20}
                                                width={20}
                                                alt={GET_NETWORK_NAME(feed.networkId)}
                                            />
                                        </span>
                                        <span className="form-check-label text-gray-800 text-ellipsis fw-medium">
                                            {ReactHtmlParser(feed.name)}
                                        </span>
                                    </label>
                                    <input
                                        className="form-check-input flex-shrink-0"
                                        type="checkbox"
                                        name=""
                                        id={`tri-conn-${feed.id}`}
                                        checked={selectedFeed ? selectedFeed.some(fed => fed.id === feed.id) : ""}
                                        onChange={() => onSelectFeed(feed)}
                                    />
                                </div>
                            )) : (
                                <div className="py-4">
                                    <h6 className="text-center fs-9 mb-0 text-gray-700">{rattingSchema?.nofeed ?? ""}</h6>
                                </div>
                            )}
                        </Dropdown.Menu>
                    </Dropdown> : <div className="py-4">
                        <p className="mb-3 sk_line sk_line_20 sk_line_height_16"></p>
                        <p className="sk_rect h-40px mb-0"></p>
                    </div>}

                    {selectedFeed?.length || codeRequest ?
                        <>
                            <h5 className="h5 mb-2 mt-6">{rattingSchema?.copyCode ?? ""}</h5>
                            {!codeRequest ? <div className="position-relative embed_code embed_code_box iframe_embcode_box rounded-3">
                                <div className='embed-border'>
                                    <code className="form-control textarea-min-row-4 p-lg-4 fs-9" id="embed_code_" onClick={() => copyText('embed_code_')}>
                                        {schema}
                                    </code>
                                </div>
                                <div className="position-absolute end-0 bottom-0 pe-5 pb-5">
                                    <OverlayTrigger placement="top" overlay={<Tooltip>{!copied ? shopifyPublish?.copy_code ?? "" : shopifyPublish?.copied_code ?? "" }</Tooltip>}>
                                        <Button variant="link" className="text-gray-300 fs-6 p-0 me-2" onClick={() => copyText('embed_code_')}>
                                            <FontAwesomeIcon type={'solid'} icon={!copied ? 'copy' : 'check'} iconClass={copied ? 'text-success' : ''} />
                                        </Button>
                                    </OverlayTrigger>
                                    <OverlayTrigger placement="top" overlay={<Tooltip>{pageContent?.emailCampaign?.shareCodeTooltip ?? ""}</Tooltip>}>
                                        <Button variant="link" className="text-gray-300 fs-6 GetShareCode" onClick={() => setOpenPopup(true)}>
                                            <FontAwesomeIcon type="solid" icon={'envelope'} iconClass="me-0" />
                                        </Button>
                                    </OverlayTrigger>
                                </div>
                            </div> : <div className="py-4">
                                <p className="sk_rect h-100px mb-0"></p>
                            </div>}
                        </> : null
                    }

                    {openPopup ? <ShareCodeModal type={5} text={rattingSchema?.popup?.text ?? ""} heading={rattingSchema?.popup?.heading ?? ""} onClose={(e) => setOpenPopup(false)} codeId={'#embed_code_'} schema={true} mailCode={mailCode} /> : null}
                </>
                : <div className="bg-gray-100">
                    <EmptyWithIcon title={ratingEmptyIcon?.title ?? ""} desc={ratingEmptyIcon?.desc ?? ""} button={{ text: ratingEmptyIcon?.text ?? "", action: () => addFeed(localStorage.getItem('active_wall')) }} isFullScreen={false} />
                </div>
            }

            <div className="rs-serp-section mt-6">
                <h4 className="h5 mb-2">{rattingSchema?.rattingInfo ?? ""} </h4>
                <img src="https://cloud.tagbox.com/react-app/media/images/serp-1.5x.png" alt="serp" className="img-fluid" />
            </div>

        </div>
    )
}
