import { ModalBody, Modal, Image } from "react-bootstrap";
import { useDispatch } from 'react-redux';
import { baseUrl, FontAwesomeIcon } from '../../../../../../Assets/AssetsHelper';
import { updateSalesModal } from '../../../../../../actions/loginActions';
import { formatTemplateText, showSalesModal } from '../../../../../../utils';
import { COMPANY_NAME } from '../../../../../../actions/api';

const getDetails = () => {

  const content = window?.APP_CONTENT
  const btn = content?.common ?? {}
  const localization = content?.localization_pop ?? {}

  let obj = {
    heading: localization?.title ?? '',
    description: formatTemplateText(localization?.dics ?? '', { company_name: COMPANY_NAME }),
    url: baseUrl(`media/products/localization.png`),
    btnText: btn?.talk_to_sales ?? '',
  }
  return obj;
}

const LocalizationPop = ({ show, handleClose, userData }) => {
  const { heading, description, url, btnText } = getDetails();
  const dispatch = useDispatch()
  return (
    <Modal show={show} onHide={handleClose} size="lg" aria-labelledby="contained-modal-title-vcenter" centered>
      <Modal.Header className="inf-modalheader border-0 d-flex flex-column justify-content-start pb-0">
        <button type="button" className="btn-close mt-1" aria-label="Close" onClick={() => handleClose()}></button>
      </Modal.Header>
      <ModalBody className="p-8 socialwall-popup-body">
        <div className="row align-itmes-center">
          <h2 className="font-weight-boldest w-100 text-center mt-0">{heading}</h2>
          <p className="mb-5 text-center">{description}</p>
          <div className="col-lg-12">
            <div className="switch_image ugc-switch-video mb-4 w-100">
              <Image isPrefix={true} className="img-fluid w-100" src={url} alt='' />
            </div>
          </div>
          <div className="col-lg-12">
            <div className="switch_text_inner text-center d-flex align-items-center justify-content-center">
              <button className={`btn btn-primary btn-lg btn-min-w mt-2 d-flex align-items-center`} onClick={() => dispatch(updateSalesModal(showSalesModal(userData)))}><span className='me-2'>{btnText}</span> <FontAwesomeIcon icon={"arrow-right"} /></button>
            </div>
          </div>
        </div>
      </ModalBody>
    </Modal>
  )
}

export default LocalizationPop