import React, { Component } from 'react'
import { connect } from 'react-redux'
import { baseUrl, FontAwesomeIcon } from '../../../../../Assets/AssetsHelper'
import { Button, Row, Col, Image, Tab, Form, Nav } from 'react-bootstrap'
import { APPLY_THEME_ACTION, CHANGE_THEME_ACTION } from '../../../../../actions/themeAction'
import { checkPermission } from '../../../../../actions/collaboratorActions'
import RequestButtonHOC from '../../CommonComponents/RequestButtonHOC'
import { BANNER_HEADER_CHANGE, CHANGE_BANNER, CHANGE_BANNER_STATUS, CHANGE_ENABLE_DISABLE, CHANGE_HEADER_LAYOUT, IS_THEME_SETTING_CHANGE, TOGGLE_LAYOUT } from '../../../../../reducer/keys'
import $ from 'jquery'
import { DashboardModal } from "@uppy/react";
import UppyUpload from '../../../../../utils/uppyUpload'
import { TEMP_URL } from '../../../../../actions/constants'
import { TOAST_ERROR } from '../../../../../utils'
import { toast } from 'react-toastify'


// const uppyUploader = new UppyUpload(null, null, true)

export const ThemeItem = ({ Index, dataTheme, action, active, themeName, header, uppyAction = () => { } }) => {
    return (<Col key={Index} className="col-4 px-1 mt-2 select-grid">
        <Button data-theme={dataTheme} variant="grid" className={`border rounded d-flex align-items-center flex-column p-1 w-100 ${dataTheme == active ? 'active' : ''}`} onClick={() => {
            if (dataTheme == 0) uppyAction();
            action();
        }}>
            <div className="w-100 h-50px p-2 mb-2 f-center">
                {!header ? <Image loading='lazy' src={baseUrl(`media/icons/${header ? 'review-header' : 'theme'}/${dataTheme}.svg`)} height={46} width={150} alt={themeName} title={themeName} fluid /> : <FontAwesomeIcon iconClass={'me-0 fs-6'} icon={dataTheme == 6 ? 'star' : dataTheme == 7 ? 'bullhorn' : dataTheme == 8 ? 'stars' : 'cloud-arrow-up'} />}
            </div>
            {<p className="mb-0 text-center fs-10">{themeName}</p>}

        </Button>
    </Col>)
}

export const RequestButton = () => {
    return (<Col className="col-4 px-1 mt-2 select-grid">
        <RequestButtonHOC>
            <Button variant="grid" className={`border rounded d-flex align-items-center flex-column p-1 w-100  btn btn-grid`}>
                <div className="w-50px h-30px mb-0 f-center bg-transparent justify-content-center d-flex align-items-center" style={{ flexGrow: 2 }}>
                    <FontAwesomeIcon type={'light'} icon={'plus-circle'} iconClass={'fs-1 me-0 text-gray-400'} />
                </div>
                <p className="mb-0 text-center fs-10">Request New Themes</p>
            </Button>
        </RequestButtonHOC>
    </Col>)
}

const bannerName = {
    6: "Review",
    7: "Social",
    8: "Star Review",
    0: "Custom",
    4: "Old Banner",
    5: "Old Banner",
    3: "Old Banner",
    2: "Old Banner",
}

class ThemeList extends Component {
    uppyUploader = new UppyUpload(null, null, true)
    state = (() => {
        const wall = this.props.appData?.wall || {}; // Store wall in a variable
        const { ThemeInfo = {}, Reviews = {} } = wall; // Destructuring for cleaner access

        return {
            selectedtheme: false,
            themeId: ThemeInfo?.id,
            isHeader: Reviews?.headerStatus,
            isWidget: Reviews?.themeStatus ?? false, // Default to false if key is missing
            headerId: Reviews?.headerType,

            bnrStatus: null, // New status show banner active 
            uppyProfileOpen: false,
        };
    })();

    onClose = () => this.setState({ selectedtheme: false, themeId: null })

    componentDidMount() {
        this.setState({ themeId: this.props.appData?.wall?.ThemeInfo?.id })

        if (this.uppyUploader) {
            this.uppyUploader.uppy.on('transloadit:result', (stepName, result) => {

                this.updateBanner('background', 'url', null, `${TEMP_URL}${result.id}.${result.ext}`, () => {
                    setTimeout(() => {
                        this.uppyUploader.uppy.cancelAll()
                        this.setState({ uppyProfileOpen: false })
                    }, 300)
                })
            })
        }
    }

    changeThemes = (themeID, type, wallId) => {
        this.setState({ selectedtheme: true, themeId: themeID })
        this.props.CHANGE_THEMES(themeID, type, wallId);
        this.props.themeSettingChange(true)

    }

    enableLayout = (type, val) => {
        const stateType = type == 'header' ? 'isHeader' : 'isWidget'
        this.setState({ [stateType]: Number(val) }, () => this.props.toggleLayout({ header: this.state.isHeader, widget: this.state.isWidget }))
    }

    changeHeader = (id) => {
        this.setState({ bnrStatus: id }, () => {
            this.props.changeBannerStatus(id); this.props.changeBannerHeader(true)
        })
    }

    componentDidUpdate(prevProps) {

        const { appData, sectionChanged, socialReview, selectedContent, resetSection } = this.props

        if (appData.save_and_publish && !prevProps.appData.save_and_publish && appData.is_themes_change) {
            this.props.APPLY_THEME(this.state.themeId);
        }

        if (appData?.wall?.ThemeInfo?.id !== prevProps.appData?.wall?.ThemeInfo?.id) {
            this.setState({ themeId: appData?.wall?.ThemeInfo?.id, headerId: appData?.wall?.Reviews?.headerType, isHeader: appData?.wall?.Reviews?.headerStatus, isWidget: appData?.wall?.Reviews?.themeStatus })
        }

        if (appData?.bannerData?.length !== prevProps.appData?.bannerData?.length) {
            const res = appData?.bannerData?.filter((item) => item.status)
            this.props.changeBannerStatus(res?.[0]?.type)
        }

        // if(prevProps.sectionChanged == false && sectionChanged == true){
        //     this.changeThemes(Number(socialReview) == 1 ? 62 : 63, Number(socialReview), selectedContent?.id)
        //     resetSection()
        // }
    }

    handleOpen = event => {
        this.setState({
            uppyProfileOpen: true,
        })
        $(".uppy-Dashboard-close").click(() => {
            this.uppyUploader.uppy.cancelAll()
            this.setState({ uppyProfileOpen: false })
        })
    }


    handleClose = event => this.setState({
        uppyProfileOpen: false,
    })

    updateBanner = (key, key2, key3, value, callback) => {
        const { appData, changeBanner, changeBannerHeader } = this.props;
        const { wall } = appData
        const updatedData = wall?.Banner?.map(item => ({ ...item, [key]: { ...item[key], [key2]: key3 ? { ...item[key][key2], [key3]: value } : value }, userUpdated: 1, wallId: localStorage.getItem('active_wall'), website_id: localStorage.getItem('website_id') }));
        changeBanner({ updatedData }); changeBannerHeader(true)
        callback();
    }

    showErrorMsg = (name) => {
        toast.error(`You don't have any active ${name} feeds! Create one to use this Banner`, { autoClose: 3000 })
    }

    render() {
        const { themeList, socialReview, selectedContent, changeThemeSection, bannerData, appData, changeEnableDisable, changeBannerHeader, changeBannerStatus, content } = this.props
        const { themeId, isHeader, isWidget, headerId, bnrStatus } = this.state
        const { themesList } = themeList

        const { wall } = appData

        const data = bannerData?.filter((item, index, self) => index === self.findIndex((t) => t.type === item.type));

        const typeZeroItems = data.filter(item => item.type === 0);
        const otherItems = data.filter(item => item.type !== 0);

        // Combine with type 0 at the end
        const chooseBanner = [...otherItems, ...typeZeroItems];
        
        const networkIds = selectedContent?.networkIds ?? []

        const isReview = networkIds?.some((item) => [4, 19, 21, 23, 33, 34, 35, 36, 37, 29, 38].includes(item))
        const isSocial = networkIds?.some((item) => ![4, 19, 21, 23, 33, 34, 35, 36, 37, 29, 38].includes(item));

        return (<div className='px-4 pb-3 mb-4'>
            {themesList ?
                <>
                    <Nav variant="pills" className="f-center mb-2 mt-4" activeKey={socialReview} onSelect={event => changeThemeSection(event)}>
                        <Nav.Item>
                            <Nav.Link className="border btn btn-primary mx-1 py-2" eventKey="1"><FontAwesomeIcon icon="grid-2" iconClass="me-1 fs-18" /> {content?.social}</Nav.Link>
                        </Nav.Item>
                        <Nav.Item className="position-relative">
                            <Nav.Link className="border btn btn-primary mx-1 py-2" eventKey="2"><FontAwesomeIcon icon="star" iconClass="me-1 fs-18" /> {content?.review}</Nav.Link>
                            {/* <span className="position-absolute badge-primary fs-10 border border-2 border-light badge m-0 badge" style={{ top: '-7px', right: '-14px', padding: '2px' }}>New</span> */}
                        </Nav.Item>
                    </Nav>

                    {![82, 84, 87].includes(wall?.ThemeInfo?.id) ? <Row className="select-group flex-wrap gx-8 pb-5 px-4">
                        <div className="f-between w-100 border-bottom px-1">
                            <Form.Label className="my-4" htmlFor="hlayout_">
                                <span className="me-2 fs-9">{content?.banner_layout}</span>
                            </Form.Label>
                            <Form.Check type="switch" id="hlayout_" title='Select Banner' checked={appData?.activeBanner === null || appData?.activeBanner === undefined ? false : wall?.Banner?.[0]?.status} onChange={event => {
                                if (appData?.activeBanner === null || appData?.activeBanner === undefined) { changeBannerStatus(socialReview == '1' ? 7 : 6); changeBannerHeader(true) }
                                else { changeEnableDisable(event.target.checked); changeBannerHeader(true) }
                            }} />
                        </div>
                        {chooseBanner?.map((Item, Index) => (
                            (socialReview == '1' && [0, 2, 3, 4, 5, 7].includes(Item.type) || socialReview == '2' && [0, 6, 8].includes(Item.type)) ? <ThemeItem key={Index} Index={Index} dataTheme={Item.type} active={Item.status ? Item.type : null} themeName={bannerName[Item.type]} action={() => checkPermission('publish', 'website', true) ?
                                (isReview && Item.type == 6 || isSocial && Item.type == 7 || Item.type < 6) ? this.changeHeader(Item.type) : this.showErrorMsg(bannerName[Item.type]) : {}} header={true} uppyAction={() => this.handleOpen()} /> : null
                        ))}
                    </Row> : null}

                    <Row className="select-group flex-wrap gx-8 border-top px-4">
                        <div className="f-between w-100 border-bottom px-1">
                            <Form.Label className="my-4" htmlFor="wlayout_">
                                <span className="me-2 fs-9">{content?.heading}</span>
                            </Form.Label>
                            {/* <Form.Check type="switch" id="wlayout_" checked={isWidget} onChange={event => checkPermission('publish', 'website',true) ? this.enableLayout('widget', event.target.checked) : {}} /> */}
                        </div>
                        {themesList.map((Item, Index) => (
                            (Item.themeType == Number(socialReview)) ? <ThemeItem key={Index} Index={Index} dataTheme={Item.themeId} active={themeId} themeName={Item.name} action={() => checkPermission('publish', 'website', true) ? this.changeThemes(Item.themeId, Item.themeType, selectedContent?.id) : {}} /> : null
                        ))}
                        {socialReview == 2 ? themesList.map((Item, Index) => (
                            ([5, 20, 19, 47, 83, 49].includes(Item.themeId)) ? <ThemeItem key={Index} Index={Index} dataTheme={Item.themeId} active={themeId} themeName={Item.name} action={() => checkPermission('publish', 'website', true) ? this.changeThemes(Item.themeId, Item.themeType, selectedContent?.id) : {}} /> : null
                        )) : null}
                        <RequestButton />
                    </Row>


                    {/* <Tab.Container id="socialReviews" activeKey={socialReview}>
                    <Tab.Content>
                        <Tab.Pane key={1} eventKey="1">
                            <Row className="select-group flex-wrap row gx-8 border-top">
                                {themesList.map((Item, Index) => (
                                    Item.themeType !=2 && <ThemeItem key={Index} Index={Index} dataTheme={Item.themeId} active={themeId} themeName={Item.name} action={() => checkPermission('publish', 'website',true) ? this.changeThemes(Item.themeId, Item.themeType, selectedContent?.id) : {}}/>
                                ))}
                                <RequestButton />
                            </Row>
                        </Tab.Pane>
                        <Tab.Pane  key={2} eventKey="2">
                            <Row className="select-group flex-wrap row gx-8">
                                <div className="f-between w-100 border-bottom">
                                    <Form.Label className="my-4" htmlFor="hlayout_">
                                        <span className="me-2 fs-9">HEADER LAYOUT</span>
                                    </Form.Label>
                                    <Form.Check type="switch" id="hlayout_" checked={isHeader} onChange={event=> checkPermission('publish', 'website',true) ? this.enableLayout('header',event.target.checked) : {}}/>
                                </div>
                                {themeHeader.map((Item, Index) => ( 
                                    <ThemeItem key={Index} Index={Index} dataTheme={Item.id} active={headerId} themeName={Item.name} action={() => checkPermission('publish', 'website',true) ? this.changeHeader(Item.id) : {}} header={true}/>
                                ))}
                            </Row>
                            <Row className="select-group flex-wrap row gx-8 border-top">
                                <div className="f-between w-100 border-bottom">
                                    <Form.Label className="my-4" htmlFor="wlayout_">
                                        <span className="me-2 fs-9">WIDGET LAYOUT</span>
                                    </Form.Label>
                                    <Form.Check type="switch" id="wlayout_" checked={isWidget} onChange={event => checkPermission('publish', 'website',true) ? this.enableLayout('widget', event.target.checked) : {}} />
                                </div>
                                {themesList.map((Item, Index) => ( 
                                    Item.themeType !=1 && <ThemeItem key={Index} Index={Index} dataTheme={Item.themeId} active={themeId} themeName={Item.name} action={() => checkPermission('publish', 'website',true) ? this.changeThemes(Item.themeId, Item.themeType, selectedContent?.id) : {}}/>
                                ))}
                            </Row>
                        </Tab.Pane>
                    </Tab.Content>
                
                </Tab.Container> */}
                </> : null}

            {this.state.uppyProfileOpen ?
                <>
                    <DashboardModal
                        id="background-image"
                        closeModalOnClickOutside
                        closeAfterFinish
                        open={this.state.uppyProfileOpen}
                        onRequestClose={this.handleClose}
                        uppy={this.uppyUploader.uppy}
                        hideUploadButton={false}
                        autoOpenFileEditor={false}
                        plugins={["GoogleDrive"]}


                        animateOpenClose={true}
                        proudlyDisplayPoweredByUppy={false}
                        inline={false}
                        showProgressDetails={true}
                        height={750}
                        browserBackButtonClose={true}
                        showRemoveButtonAfterComplete={false}
                    />
                    <div>
                        <p>Recommended size: 1920 x 200 px | Supported Formats: .png or  .jpg | Maximum Image Size: 5Mb</p>
                    </div>
                </>
                : null}

        </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        appData: state.appData
    }
}
const mapDispatchToProps = dispatch => {
    return {
        CHANGE_THEMES: (id, type, wallKey) => dispatch(CHANGE_THEME_ACTION(id, type, wallKey)),
        APPLY_THEME: (id) => dispatch(APPLY_THEME_ACTION(id)),
        selectHeader: (id) => dispatch({ type: CHANGE_HEADER_LAYOUT, payload: id }),
        toggleLayout: (data) => dispatch({ type: TOGGLE_LAYOUT, payload: data }),
        changeBannerStatus: (id) => dispatch({ type: CHANGE_BANNER_STATUS, payload: id }),
        changeEnableDisable: (id) => dispatch({ type: CHANGE_ENABLE_DISABLE, payload: id }),
        changeBanner: (data) => dispatch({ type: CHANGE_BANNER, payload: data }),
        changeBannerHeader: (data) => dispatch({ type: BANNER_HEADER_CHANGE, payload: data }),
        themeSettingChange: (data) => dispatch({ type: IS_THEME_SETTING_CHANGE, payload: data })
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(ThemeList);