import { useCallback, useEffect, useMemo, useState } from "react";
import { useDispatch } from "react-redux";
import { Form, Alert, Dropdown, Image } from "react-bootstrap";
import { WEBITE_LANGUAGE_CHANGE } from "../../../../../reducer/keys";
import { updateWebsiteLanguage } from "../../../../../actions/appsMoreAction";
import { LANGUAGE_SUPPORTS } from "../../../../../actions/constants";
import { MediaBaseUrl } from "../../../../../actions/api";
import { SWAL_FIRE, TOAST_LOADING, TOAST_DISMISS, formatTemplateText, TOAST_UPDATE } from "../../../../../utils";
import { FontAwesomeIcon } from "../../../../../Assets/AssetsHelper";
import CustomToolTip from "../../CommonComponents/CustomToolTip";
import { CHANGE_WEBSITE_WALL } from "../../../../../actions/websiteAction";

const WebsiteLanguage = ({ wallData, content }) => {
    const [selectedLanguage, setSelectedLanguage] = useState(null);
    const dispatch = useDispatch();

    const languages = useMemo(() => [{ label: 'Default', value: '' }, ...LANGUAGE_SUPPORTS], [])

    const getFlagUrl = useCallback((lang) => {
        return lang?.value ? `${MediaBaseUrl}media/images/flags/${lang.value}.svg` : `https://ui-avatars.com/api/?name=${lang?.label ?? ""}&background=000&color=fff&length=1`;
    }, [])

    const applySettings = useCallback((lang) => {
        const selLang = lang?.value;
        const commonContent = window?.APP_CONTENT?.common ?? {};
        const prevLang = wallData?.ThemeInfo?.language_code ?? "";
        if(selLang != prevLang){
            SWAL_FIRE({
                title: commonContent?.are_you_sure ?? "",
                text: formatTemplateText(commonContent?.language_change_website ?? "", { lang: lang?.label ?? "" }),
                icon: "warning",
                showCancelButton: true,
                confirmButtonText: formatTemplateText(commonContent?.language_primary_btn ?? "", { lang: lang?.label ?? "" }),
                cancelButtonText: commonContent?.language_secondary_btn ?? "",
            }).then((result) => {
                if (result.value) {
                    const toastId = TOAST_LOADING();
                    const wallId = localStorage.getItem('active_wall');
                    updateWebsiteLanguage({
                        wallId,
                        language_code: selLang,
                        websiteId: localStorage.getItem('website_id'),
                    }).then((response) => {
                        dispatch({ type: WEBITE_LANGUAGE_CHANGE, payload: selLang });
                        if(response?.data?.responseCode == 200) dispatch(CHANGE_WEBSITE_WALL(wallId, wallData?.ThemeStyle?.totalPosts ?? 20, wallData.ThemeInfo?.id));
                        TOAST_UPDATE(toastId, "success", response?.data?.response_message ?? response?.data?.responseMessage ?? "");
                    }).catch(ex => TOAST_UPDATE(toastId, "error", ex?.response?.data?.response_message ?? ex?.response?.data?.responseMessage ?? "")).finally(() => {
                        setTimeout(() => TOAST_DISMISS(toastId), 3000);
                    });
                }
            })
        }
    }, [selectedLanguage, wallData])

    useEffect(() => {
        const newLang = wallData?.ThemeInfo?.language_code ?? "";
        if (selectedLanguage != newLang) {
            const language = languages.find((lang) => lang.value == newLang);
            setSelectedLanguage(language);
        }
    }, [wallData]);

    return <div className="w-100">
        <div className="mt-2">
            <Form.Label className="mb-2" htmlFor={`su_language`}>
                {content?.website_title ?? ""}
                <CustomToolTip placement="top" overlayText={content?.localization_info ?? ""}>
                    <span className={`svg-icon svg-icon-md svg-icon-dark-75 ms-2`}>
                        <FontAwesomeIcon type={'solid'} icon={'circle-info'} iconClass={'text-muted'} />
                    </span>
                </CustomToolTip>
            </Form.Label>
            <Dropdown className="w-100" id="su_language" >
                <Dropdown.Toggle className="text-capitalize text-start w-100 border position-relative" variant="" id="dropdown-basic">
                    <Image className={`me-2 object-fit-${selectedLanguage?.value ? "contain" : "cover"} border`} src={getFlagUrl(selectedLanguage)} height={17} width={28} fluid style={selectedLanguage?.value ? {} : { height: "20px" }} /> {selectedLanguage?.label ?? "Default"}
                </Dropdown.Toggle>
                <Dropdown.Menu>
                    {languages?.map((lang) => {
                        return <Dropdown.Item className="text-capitalize" onClick={() => applySettings(lang)} key={lang.value}><Image className={`me-2 object-fit-${lang.value ? "contain" : "cover"} border`} src={getFlagUrl(lang)} height={17} width={28} fluid style={lang.value ? {} : { height: "20px" }} />{lang.label}</Dropdown.Item>
                    })}
                </Dropdown.Menu>
            </Dropdown>
        </div>
        <Alert className="mt-2 mb-0 rounded-0 fs-9 p-3" variant="info">{content?.website_desc ?? ""}</Alert>
    </div>
}

export default WebsiteLanguage