import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Image } from 'react-bootstrap'
import { Link } from 'react-router-dom'
import EmbedCode from './EmbedCode';
import TagBase from './TagBase'
import ShopifyPublish from '../ShopifyPublish/ShopifyPublish'
import BigCommercePublish from '../BigCommercePublish'
import { GET_BIGCOMMERCE_PUBLISH_DATA } from '../../../../../../actions/bicommerceAction'
import { LOGOBRAND, DOMAIN_NAME, COMPANY_NAME } from '../../../../../../actions/api'
import { formatTemplateText, shopifyApp } from '../../../../../../utils';

export const NumberBullet = ({ count }) => <span className='d-inline-block text-center h-20px w-20px rounded-circle me-2 bg-success text-gray-100 fs-9 flex-shrink-0' style={{ lineHeight: '20px' }}>{count}</span>
class PublishSteps extends Component {
    state = { searchData: [], isLoading: false, searchPost: null, openDropdwn: false, isPreview: false, dataTag: false, copied: false }
    componentDidMount() {
        const { isBigCommerce } = this.props
        if (isBigCommerce) {
            this.props.GET_BIGCOMMERCE_PUBLISH_DATA()
        }
    }


    render() {
        const { platform, galleryType, allTags, isShopify, isBigCommerce, isShoppable, content } = this.props
        const { dataTag } = this.state
        const { commonContent, plateform, steps } = content
        const usingTags = galleryType == 3 ? steps?.usingtag : '';
        var count = 0;
        return (<div className='mb-10 pb-10'>
            {shopifyApp() ?
                <ShopifyPublish galleryType={galleryType} allTags={allTags} />
                : <div>
                    {platform == 1 ?
                        <h5 className="mb-5"> {commonContent?.heading ?? ""}  {usingTags}</h5>
                        :
                        platform == 2 ?
                            <h5 className="mb-5">{`${steps?.Publish} ${isShoppable ? steps?.shoppable : ""} ${plateform[platform]?.steps} `} {usingTags}</h5>
                            :
                            platform == 3 ?
                                <h5 className="mb-5"> {plateform[platform]?.steps} {usingTags}</h5>
                                :
                                platform == 4 ?
                                    <h5 className="mb-5"> {plateform[platform]?.steps} {usingTags}</h5>
                                    :
                                    platform == 5 && !isBigCommerce ?
                                        <h5 className="mb-5">{`${steps?.Publish} ${isShoppable ? steps?.shoppable: ""} ${plateform[platform]?.steps}`} {usingTags}</h5>
                                        :
                                        platform == 6 ?
                                            <h5 className="mb-5"> {plateform[platform]?.steps} {usingTags}</h5> :
                                            platform == 7 ? <h5 className="mb-5"> {plateform[platform]?.steps} {usingTags}</h5> : 
                                            platform == 8 ?
                                            <h5 className="mb-5"> {plateform[platform]?.steps}  {usingTags}</h5>
                                            :
                                            platform == 9 ?
                                            <h5 className="mb-5"> {plateform[platform]?.steps} {usingTags}</h5>
                                            : null}
                    {(platform == 2 || (platform == 5 && !isBigCommerce) || platform == 3) ?
                        <div className='px-lg-3'>
                            <div className='rounded-1 border d-flex p-lg-5 p-4 align-items-center mb-5'>
                                <div className='d-flex flex-grow-1 align-items-center'>
                                    <Image className='me-2' loading='lazy' src={LOGOBRAND} alt={DOMAIN_NAME} title={DOMAIN_NAME} height={50} width={50} fluid />
                                    <div className='d-flex flex-column'>
                                        <p className='h3 mb-1'>{COMPANY_NAME} App</p>
                                        <p className='mb-0'><Link id="instruction_link" target="_blank" to={platform == 2 ? 'https://tagshop.ai/support/taggbox-shopify-app/' : platform == 3 ? 'https://tagshop.ai/support/how-to-use-tagshop-bigcommerce-app/' : 'https://tagshop.ai/support/how-to-use-tagshop-bigcommerce-app/'} className='fw-medium' title={steps.instructions}>{steps.instructions}</Link></p>
                                    </div>
                                </div>
                                <div>
                                    <Link id="install_app" target="_blank" to={platform == 2 ? 'https://apps.shopify.com/instagram-shop?utm_source=Commerce&utm_medium=Editor&utm_campaign=Shopify-app' : platform == 3 ? `https://wordpress.org/plugins/${DOMAIN_NAME}-widget/` : 'https://www.bigcommerce.com/apps/taggshop/'} className='btn btn-primary' title={steps.installApp}>{steps.installApp}</Link>
                                </div>
                            </div>
                            <div className='f-center border-top my-10'>
                                <span className='h-40px w-40px fs-8 f-center fw-semibold rounded-circle border bg-body' style={{ marginTop: -20 }}>OR</span>
                            </div>
                        </div>
                        : null}

                    {platform == 2 ?
                        <h5 className="mb-5">{plateform[platform]?.publish_heading}</h5>
                        :
                        platform == 5 ? isBigCommerce ? <BigCommercePublish galleryType={galleryType} allTags={allTags} /> :
                            <h5 className="mb-5">{plateform[platform]?.publish_heading}</h5>
                            : null
                    }

                    {galleryType == 3 ? <TagBase allTags={allTags} count={count + 1} updateData={(val) => this.setState({ dataTag: val })} /> : null}

                    {!(isBigCommerce && platform == 5) ? <div className='px-lg-3'>
                        <EmbedCode platform={platform} galleryType={galleryType} allTags={allTags} count={count + galleryType == 3 ? 2 : 1} dataTag={dataTag} />
                        

                        {platform == 1 ?
                            <>
                                <p className="h6 mb-4"><NumberBullet count={count + galleryType != 1 ? 4 : 3} /> {commonContent?.publishDesc ?? ""}</p>
                                <div className='ps-8'>
                                     {
                                       Object.values( plateform[1].dics).map((text, index) => (
                                            <p key={index}>
                                            { index === 2
                                                ? formatTemplateText(text, { replace_text: isShoppable ? steps?.Shoppable : "" })
                                                : text}
                                            {
                                                index === 0 ?  <><strong> { plateform[1][index][1]} </strong> { plateform[1][index][2] }  </>  : null
                                            } 
                                            </p>
                                      ))
                                    }
                                </div>
                            </>
                            : platform == 2 ?
                                <p className="h6 mb-4"><NumberBullet count={count + galleryType != 1 ? 4 : 3} /> {plateform[2].publishDesc} </p>
                                : platform == 3 ?
                                    <>
                                        <p className="h6 mb-4"><NumberBullet count={count + galleryType != 1 ? 4 : 3} /> { commonContent?.publishDesc ?? ""}</p>
                                        <div className='ps-8'>
                                             {
                                                Object.values( plateform[3].dics).map((text, index) => <p>{text}</p>)
                                             }
                                        </div>
                                    </>
                                    : platform == 4 ?
                                        <>
                                            <p className="h6 mb-4"><NumberBullet count={count + galleryType != 1 ? 4 : 3} /> { commonContent?.publishDesc ?? ""}</p>
                                            <div className='ps-8'>

                                                  {  Object.values( plateform[1].dics).map((text, index) => (
                                                        <p key={index}>
                                                        {index === 2
                                                            ? formatTemplateText(text, { replace_text: isShoppable ? steps?.Shoppable : "" })
                                                            : text}

                                                             {
                                                                index === 0 ?  <><strong> { plateform[1][index][1]} </strong> { plateform[1][index][2] }  </>  : null
                                                             } 
                                                        </p>
                                                    ))
                                                    
                                                  }
                                          
                                            </div>
                                        </>
                                        : platform == 5 ?
                                            <p className="h6 mb-4"><NumberBullet count={count + galleryType != 1 ? 4 : 3} /> { plateform[5].publishDesc } </p>
                                            : platform == 6 ?
                                                <>
                                                    <p className="h6 mb-4"><NumberBullet count={count + galleryType != 1 ? 4 : 3} /> { commonContent?.publishDesc ?? ""}</p>
                                                    <div className='ps-8'>

                                                       {  Object.values( plateform[6].dics).map((text, index) => (
                                                                <p key={index}>
                                                                { [1,3].includes(index)
                                                                    ? formatTemplateText(text , { replace_text: isShoppable ? steps?.Shoppable : "" })
                                                                    : text}

                                                                    {
                                                                        index === 0 ? <strong> { plateform[platform][1]} </strong> : null
                                                                    } 
                                                                </p>
                                                            ))
                                                        }

                                                    </div>
                                                </>
                                                : platform == 7 ?
                                                    <>
                                                        <p className="h6 mb-4"><NumberBullet count={count + galleryType != 1 ? 4 : 3} /> { commonContent?.publishDesc ?? ""}</p>
                                                         <div className='ps-8'> 
                                                           {Object.values( plateform[7].dics).map((text, index) => {
                                                                  const replacements = {
                                                                    1: { value:  plateform[7].dics[2], vars: { replace_text: COMPANY_NAME } },
                                                                    2: { value:  plateform[7].dics[3], vars: { replace_text: isShoppable ? steps?.Shoppable : "" } }
                                                                };

                                                                if (replacements[index]) {
                                                                    const { value, vars } = replacements[index];
                                                                    return (
                                                                    <p key={index}>
                                                                        {formatTemplateText(value ?? "", vars)}
                                                                    </p>
                                                                    );
                                                                }

                                                                return <p key={index}>{text}</p>;
                                                            })}

                                                        </div>
                                                    </>
                                                    :
                                                    platform == 8 ?
                                                    <>
                                                        <p className="h6 mb-4"><NumberBullet count={count + galleryType != 1 ? 4 : 3} /> { commonContent?.publishDesc ?? ""}</p>
                                                        <div className='ps-8'>
                                                            {
                                                                Object.values( plateform[8].dics).map((text, index) =>  <p>{text}</p> )
                                                            }
                                                        </div>
                                                    </>
                                                    :
                                                    platform == 9 ?
                                                    <>
                                                        <p className="h6 mb-4"><NumberBullet count={count + galleryType != 1 ? 4 : 3} /> { commonContent?.publishDesc ?? ""}</p>
                                                        <div className='ps-8'>
                                                            {
                                                                Object.values( plateform[9].dics).map((text, index) =>  <p>{text}</p> )
                                                            }
                                                        </div>
                                                    </>
                                                    :
                                          null
                        }

                    </div> : null}
                </div>}
        </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        BigcommerceData: state?.BigcommerceData?.BigcommerceData,
    }
}
const mapDispatchToProps = dispatch => {
    return {
        GET_BIGCOMMERCE_PUBLISH_DATA: () => dispatch(GET_BIGCOMMERCE_PUBLISH_DATA())
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(PublishSteps);