import React, { Component, Suspense } from 'react'
import { Container, Tab, Card, Button, Image, Nav } from 'react-bootstrap';
import { baseUrl, FontAwesomeIcon } from '../../../../Assets/AssetsHelper';
import { connect } from "react-redux";
import ContentSwitcher from '../CommonComponents/ContentSwitcher';
import GenerateCode from './GenerateCode';
import Theme from './Theme';
import PlatformAside from './GenerateCode/PlatformAside'
import CTAButtonStyle from './CTAButtonStyle'
import { GET_THEME_LIST, GET_WALL_DETAILS } from '../../../../actions/themeAction';
import Customization from './Customization';
import { GET_CTA_ACTION } from '../../../../actions/moderationActions';
import { getFontVariants } from '../../../../actions/appsMoreAction';
import { ACTIVE_WEBSITE_ID_REDUCER, CALLBACK_STORE, IS_WALL_CHANGE, SAVE_AND_PUBLISH, SET_ALL_UPDATED } from '../../../../reducer/keys';
import { getURLQuery, scrolldiv, shopifyApp, shopifyForm, SWAL_FIRE, TOAST_ERROR, TOAST_SUCCESS, validURL } from '../../../../utils';
import { GET_ALL_WEBSITE_LIST, UPDATE_WEBSITE_WALL, CHANGE_WEBSITE, CHANGE_WEBSITE_WALL } from '../../../../actions/websiteAction';
import ErrorFound from '../../../ErrorFound';
import { Loader } from '../CommonComponents/Loader';
import { getUserInfo_afterChanges } from '../../../../actions/loginActions';
import { TOGGLE_ADD_WEBSITE } from '../../../../reducer/keys';
import { isWebsiteChanged } from '../../../../utils/checkForNavigation';
import { SNAPUP_DETAILS_ACTION } from '../../../../actions/addFeedActions';
import ThemeList from './Theme/ThemeList'
import CardAside from '../CommonComponents/CardAside';
import WebsiteSwitcher from '../CommonComponents/WebsiteSwitcher';
import withRouter from "../../../routes/WithRouter";
import "./website.scss"
import ErrorPage from './ErrorPage';
import ThemeConditionError from './ErrorPage/ThemeConditionError';
import EmptyWithIcon from '../CommonComponents/Empty/EmptyWithIcon';
import HeadFont from './Customization/HeadFont';
import ThemePreview from "./GenerateCode/ThemePreview";
import { checkPermission } from '../../../../actions/collaboratorActions';
import PublishSubheader from '../CommonComponents/PublishSubheader';
import WebsiteSkel from './WebsiteSkel';
import SubHeaderCrumbs from '../CommonComponents/SubHeader';


const isBigCommerce = window.location.pathname.includes("/bigcommerce/") ? true : false;
const CreateWebsite = ({ toggle, TOGGLE_ADD_WEBSITE }) => {
    return <div className="f-center py-4 w-100" style={{ height: 'calc(100vh - 95px)' }}>
        <div className="d-flex flex-column align-items-center">
            <Image data-mode="light" loading='lazy' className="mb-5" src={baseUrl(`media/icons/color-icons/media.svg`)} height={120} width={120} alt={'media'} title='media' fluid />
            <Image data-mode="dark" loading='lazy' className="mb-5" src={baseUrl(`media/icons/color-icons/media-light.svg`)} height={120} width={120} alt={'media'} title='media' fluid />
            <h4 className={`text-gray-dark fw-semibold text-center`}>Create a website, to start! </h4>
            <p className={"fs-7"}>Oops! Looks like you have deleted your website, create one by simply clicking on the button below.</p>
            <Button variant='primary' className="mb-2 btn-min-w" onClick={() => TOGGLE_ADD_WEBSITE()}>
                Create Website
            </Button>
        </div>
        <WebsiteSwitcher title="Website" addNew={toggle} noList={true} />
    </div>
}

const WebsiteLoader = () => <div style={{ minHeight: 'calc(100vh - 170px)' }}><Loader bg="body" /></div>

const NavOption = ({isShoppable, themeId}) => {
    return <Nav variant="underline" className='justify-content-center border-bottom'>
        <Nav.Item className=''>
            <Nav.Link eventKey="theme">
                Theme
            </Nav.Link>
        </Nav.Item>
        <Nav.Item className='ms-2'>
            <Nav.Link eventKey="customization" disabled={!checkPermission('publish', 'website', true, true)}>
                Customization
            </Nav.Link>
        </Nav.Item>
        {isShoppable && themeId!=87 ? <Nav.Item className='ms-2' >
            <Nav.Link eventKey="cta" disabled={!checkPermission('publish', 'website', true, true)}>
                Product CTA
            </Nav.Link>
        </Nav.Item> : null}
    </Nav>
}

class Website extends Component {
    constructor(props) {
        super(props);
        this.state = {
            footer_action1: false, footer_action2: true, footer_action3: false, galleryType: 1, banner: false, activeKey: '', activeGallery: [], changeSetting: false, spinner: false, mobilePrewiew: false, isWallChange: false, previewRerender: true, minNumberPost: 1, previewKey: 'script',
            width: '', isSavedList: false, selectedWall: '', platform: isBigCommerce ? 5 : 1, publishOption: 1, selectLoader: false, filterToggle: false, socialReview: 1, sectionChanged: false,
            viewMode: 'desktop', isUpdate:false
        };
    }
    userInitiatedClick = false;
    userGenClick = false;
    pageContent = window.APP_CONTENT;
    isShopify = shopifyApp();
    componentWillMount() {
        const { themeData } = this.props
        !themeData?.themeList?.length && this.props.GET_THEME_LIST();
    }

    handleMouseDown = () => {
        this.userInitiatedClick = true;
    };

    handleSaveClick = () => {
        this.props.SAVE_AND_PUBLISH(true);
        // Check flag and pass it, then reset
        this.applyWebsite_wall(this.userInitiatedClick);
        this.userInitiatedClick = false;
    }

    handleGenMouseDown = () => {
        this.userGenClick = true;
    };

    handleGenClick = () => {
        this.onGenerateCode(this.userGenClick)
        this.userGenClick = false
        scrolldiv(`#scrollTab`)
    }

    componentDidMount() {
        const { websiteData, ctaData, GET_ALL_WEBSITE_LIST } = this.props
        if(!websiteData?.websiteList?.length) GET_ALL_WEBSITE_LIST();


        if (this.isShopify) {
            window.addEventListener('onSaveClicked', this.saveWebsite)
            window.addEventListener('onDiscardClicked', this.discardSetting)
        }


        const { allWalls, appData, GET_CTA_ACTION, CHANGE_WEBSITE_WALL } = this.props;
        const activeWallId = localStorage.getItem("active_wall");
        const firstWall = allWalls?.[0] || null;

        let gallery = allWalls?.find(({ id }) => String(id) == String(activeWallId)) || firstWall;
        if (!ctaData) GET_CTA_ACTION(appData?.wall);

        let footerAction = {}
        const query = getURLQuery()
        if(query?.publishCode) footerAction = { footer_action1: true, footer_action2: false, footer_action3: false }

        this.setState({ activeGallery: gallery, ...footerAction });

        if (gallery?.id && appData?.wall?.ThemeInfo?.id) {
            CHANGE_WEBSITE_WALL(
                gallery.id,
                appData?.wall?.ThemeStyle?.totalPosts || 20,
                appData?.wall?.ThemeInfo?.id || 62
            );
        } else {
            CHANGE_WEBSITE_WALL(firstWall?.id, 20, 62);
            this.setState({ activeGallery: firstWall });
        }

        window.addEventListener('resize', this.handleWindowResize);
        this.selectedWebsite()
    }

    componentWillUnmount() {
        if (this.isShopify) {
            shopifyForm('hide')
            window.removeEventListener('onSaveClicked', this.saveWebsite)
            window.removeEventListener('onDiscardClicked', this.discardSetting)
        }
    }

    changeGalleryType = (e) => this.setState({ galleryType: e, previewKey: 'script' })

    changePlatform = e => this.setState({ platform: e })

    selectedWebsite = () => {
        const { websiteList } = this.props.websiteData
        var webData = websiteList?.find((x) => x.wall?.ThemeInfo?.webId == localStorage.getItem('website_id'));
        const web = webData && webData?.wall ? webData : websiteList && websiteList[0] ? websiteList[0] : ''
        if (web) this.setState({ selectedWall: web, socialReview: [85, 82, 84, 80, 79].includes(web?.wall?.ThemeInfo?.id) ? 2 : 1 })
    }

    ChangeGallery = (id, noChange, webData) => {
        const { allWalls, appData } = this.props
        var gallery = allWalls?.find((item) => item.id == id);
        this.setState({ activeGallery: gallery, selectLoader: false })
        localStorage.setItem("active_wall", gallery?.id)
        if (!noChange) this.props.WALL_CHANGED()

        if (webData?.wall?.ThemeInfo || appData?.wall?.ThemeInfo?.id || noChange) {
            this.props.CHANGE_WEBSITE_WALL(
                gallery?.id,
                webData?.wall?.ThemeStyle?.totalPosts ?? appData?.wall?.ThemeStyle?.totalPosts ?? 20,
                webData?.wall?.ThemeInfo?.id ?? appData?.wall?.ThemeInfo?.id ?? 62
            );
        }

    }

    changeActiveTab = () => {
        var targets = document.querySelectorAll('.publishTab');
        targets.forEach(function (target) {
            if (target.classList.contains('active')) target.classList.remove('active');
        });
        document.querySelector('#html-tab').classList.add('active');
    }

    handleViewChange = (mode) => {
        this.setState({ viewMode: mode });
    }

    componentDidUpdate(prevProps) {
        const { websiteData, allWalls, appData } = this.props

        if (websiteData?.websiteList.length != prevProps.websiteData?.websiteList.length) {
            if (websiteData?.websiteList.length > 0) {
                let wall = null
                let webD = websiteData?.websiteList?.find((x) => x.wall?.ThemeInfo?.webId == localStorage.getItem('website_id'))
                webD = webD ? webD : websiteData?.websiteList[0]
                if (allWalls && allWalls.length > 0) {
                    wall = allWalls.find(item => item.id == webD?.wall?.ThemeInfo?.wallId)
                    wall = wall ? wall : allWalls[0]
                }
                this.props.GET_APP_DATA(webD)
                this.setState({ selectedWall: webD, activeGallery: wall, footer_action1: false, footer_action2: true, footer_action3: false, socialReview: [85, 82, 84, 80, 79].includes(webD?.wall.ThemeInfo?.id) ? 2 : 1 }, () => wall ? this.ChangeGallery(wall.id, 'noChange', webD) : {})
                localStorage.setItem('website_id', webD.wall?.ThemeInfo?.webId);
            }
            else {
                this.setState({ selectedWall: null, activeGallery: allWalls && allWalls.length > 0 ? allWalls[0] : null, footer_action1: false, footer_action2: true, footer_action3: false }, () => {
                    if (allWalls & allWalls.length > 0) this.ChangeGallery(allWalls[0].id, 'noChanges', null)
                })
                localStorage.setItem('website_id', '');
            }
        }

        if (prevProps.allWalls?.length !== allWalls?.length && this.state.selectedWall) {
            const wall = allWalls?.find(item => item.id == this.state.selectedWall.wall?.ThemeInfo?.wallId) || allWalls?.[0] || null;
            this.ChangeGallery(wall?.id || null, 'noChanges', null);
        }

        if (appData?.live_preview_loader && appData?.isPostLoading && !prevProps.appData?.live_preview_loader && !prevProps.appData?.isPostLoading) this.setState({ previewRerender: false })

        if (this.isShopify && (appData?.is_wall_change || appData?.is_themes_change || appData?.is_backgroun_change || appData?.is_banner_IMG_change || appData?.is_banner_change || appData?.is_card_style_change || appData?.is_theme_setting_change || appData?.is_custome_css_change || appData?.is_magic_upload_change || appData?.is_cta_setting_change || appData?.is_utm_tracker_change || appData?.isHeaderUpdate || appData?.is_card_cta_change || appData?.is_hash_changed)) {
            shopifyForm()
        }
    }

    applyWebsite_wall = (isSave) => {
        const { activeGallery,  isUpdate } = this.state
        const { wall } = this.props.appData
        const { emailCampaign  } = this.pageContent

        if (wall.ThemeInfo?.gtStatus === 1 && !wall.ThemeInfo?.gtCode) return TOAST_ERROR(emailCampaign?.toastarMsg?.trackingId), false;


        const obj = { wallId: activeGallery ? activeGallery.id : localStorage.getItem('active_wall'), id: localStorage.getItem('website_id') }
        this.setState({ spinner: true })

        if ((wall?.ThemeStyle?.modal.action == 3 && !validURL(wall?.ThemeStyle?.modal.redirect) || (wall?.CardStyle?.globalCTA?.status == 1 && !validURL(wall?.CardStyle?.globalCTA?.url)))) {
            TOAST_ERROR(wall?.ThemeStyle?.modal.action == 3 && !validURL(wall?.ThemeStyle?.modal.redirect) ? emailCampaign?.toastarMsg?.valid_url : emailCampaign?.toastarMsg?.cta_url )
            this.props.SAVE_AND_PUBLISH(false)
            this.setState({ spinner: false })
        }
        else {
            setTimeout(() => {
                UPDATE_WEBSITE_WALL(obj).then(res => {
                    const { responseCode, responseMessage } = res.data
                    this.setState({ spinner: false, isWallChange: false })
                    if (responseCode == 200) {
                        if(this.props.appData?.callBackAction && !isSave) this.props.appData?.callBackAction()
                        TOAST_SUCCESS(responseMessage);
                        this.props.GET_ALL_WEBSITE_LIST()
                        this.props.SET_ALL_UPDATED();
                        setTimeout(() => {
                            this.setState({isUpdate:!isUpdate})
                        }, 1000);
                        if (this.isShopify) {
                            window.saveBar.set({ saveAction: { loading: false } })
                            setTimeout(() => {
                                shopifyForm('hide')
                            }, 200);
                        }
                    } else TOAST_ERROR(responseMessage);
                }).catch((error) => console.warn(error))
            }, 1000);
        }
    }

    saveWebsite = () => {
        window.saveBar.set({ saveAction: { loading: true } })
        this.props.SAVE_AND_PUBLISH(true);
        this.applyWebsite_wall()
    }

    discardSetting = () => {
        const webData = this.props.websiteData?.websiteList?.find(x => x.wall?.ThemeInfo?.webId == localStorage.getItem('website_id'));
        const element = document.querySelector('#discardSetting');
        if (!webData) return;

        const updateData = () => {
            localStorage.setItem('active_wall', webData.wall?.ThemeInfo?.wallId);
            this.props.GET_APP_DATA(webData, () => this.isShopify ? shopifyForm('hide') : {});
        };

        if (this.isShopify) {
            this.changeWebsite(webData.wall, true);
            updateData();
        }

        else {  
            if (!element?.getAttribute('data-nav')) {
                SWAL_FIRE({
                    title: this.pageContent?.common?.are_you_sure ?? "",
                    icon: "warning",
                    showCancelButton: true,
                    confirmButtonText: "Discard",
                    cancelButtonText: "Cancel",
                }).then(res => {
                    if (res.isConfirmed) {
                        this.changeWebsite(webData.wall, true);
                        updateData();
                    }
                });
            } else {
                updateData();
            }
        }
    };

    onGenerateCode = (isDiscard) => {
        const { websiteList } = this.props.websiteData;
        const currWebsite = localStorage.getItem("website_id");
        const wallDetails = websiteList?.find(x => x.wall.ThemeInfo?.webId == currWebsite);
        const element = document.querySelector('#discardSetting');

        const updateData = () => {
            localStorage.setItem('active_wall', wallDetails.wall?.ThemeInfo?.wallId);
            this.props.GET_APP_DATA(wallDetails, () => this.isShopify ? shopifyForm('hide') : {});
        };

        const updateFooterState = () => this.setState({ footer_action2: false, footer_action1: true, footer_action3: true });
        this.props.storeCallback(updateFooterState)
        if (element?.getAttribute('data-nav') ) {
            updateData()
            if(isDiscard) setTimeout(updateFooterState, 500);
        } else if (wallDetails?.wall.ThemeInfo?.wallId && isDiscard) {
            isWebsiteChanged();
        } 

        if(isDiscard == false && this.isShopify) setTimeout(updateFooterState, 500)
        // else {
        //     this.props.SAVE_AND_PUBLISH(true);
        //     this.applyWebsite_wall();
        //     setTimeout(() => isWebsiteChanged(), 500);
        // }
        document.getElementById("subtree-container")?.remove();
    }


    changeWebsite = (item, skip) => {
        const applyChange = () => this.changeWebData(item);
        this.props.storeCallback(applyChange)
        isWebsiteChanged(applyChange, skip)
    }

    changeWebData = (item) => {
        const { websiteData, navigate, allWalls } = this.props
        const { ThemeInfo } = item
        this.setState({ selectedWall: null, selectLoader: true, activeGallery: null })
        localStorage.setItem('website_id', ThemeInfo?.webId);

        const platform = window.location.pathname.match(/\/(shopify|bigcommerce)\//)?.[1];
        navigate(`/website/${platform || 'publish'}/${platform ? localStorage.getItem('website_id') : ThemeInfo?.webId}`);

        var webData = websiteData?.websiteList?.find((x) => x.wall.ThemeInfo?.webId == ThemeInfo?.webId);

        this.props.GET_APP_DATA(webData, () => this.isShopify ? shopifyForm('hide') : {});
        this.props.ACTIVE_WEBSITE_ID();

        const wall = allWalls?.length ? allWalls.find(item => item.id == webData?.wall?.ThemeInfo?.wallId) : null;

        setTimeout(() => {
            this.setState(
                {
                    selectedWall: webData,
                    activeGallery: wall,
                    selectLoader: false,
                    socialReview: [85, 82, 84, 80, 79].includes(webData?.wall?.ThemeInfo?.id) ? 2 : 1
                },
                () => this.ChangeGallery(wall?.id ?? allWalls?.[0]?.id ?? {}, 'noChange', webData)
            );
        }, 100);
    }


    changeThemeSection = (event) => {
        const { socialReview, selectedWall } = this.state
        // () => this.changeWebsite(selectedWall.wall.Wall, true)
        if (socialReview != event) {
            if (Number(event) == selectedWall?.wall?.ThemeInfo?.themeType) {
                this.setState({ socialReview: event, sectionChanged: false })
            }
            else this.setState({ socialReview: event, sectionChanged: true })
        }
    };

    closeAside = () => this.setState({ filterToggle: false })

    render() {
        const { footer_action1, footer_action2, footer_action3, activeKey, activeGallery, changeSetting, previewRerender, selectedWall, selectLoader, platform, galleryType, previewKey, filterToggle, socialReview, sectionChanged, isUpdate } = this.state;
        const { themeData, allWalls, appData, websiteData, allFont, authorized, allTags } = this.props
        const authData = authorized?.userData?.user_data?.sessionData
        const websiteUrl = authData?.createFrom.toLowerCase() == 'shopify' ? `shopify` : authData?.createFrom.toLowerCase() == 'bigcommerce' ? `bigcommerce` : 'publish'
        const shopifyAppKey = this.isShopify;

        const {bannerData} = appData;
        const { publish, emailCampaign } = this.pageContent
        const content = publish ?? {}
        const breadCrumbs = window?.APP_CONTENT?.['sideBar'] || {};
            
        return <>
            {websiteData?.websiteList?.length ? <SubHeaderCrumbs crumbs={[{ name: selectedWall?.wall?.ThemeInfo?.wallName ?? "", icon: "paper-plane" },{ name: breadCrumbs?.home ?? "", link: "/home"},{ name: breadCrumbs?.publish_ ?? "", link: "/publish"}]} publishSwitcher={true} changePublish={this.changeWebsite} publishList={websiteData?.websiteList} selectedPublish={selectedWall?.wall} isWebsite={true} title='Website'/> : null}
            {/* {shopifyAppKey ? <PublishSubheader websiteUrl={websiteUrl} /> : null} */}
            <div className={`d-flex flex-column flex-column-fluid full-content ${this.state.galleryType == 4 ? 'rating-gallery' : ''}`}>
                <Container fluid className={`container-publish-inn`}>
                    <HeadFont fonts={allFont} />
                    {websiteData?.websiteList?.length ?
                        <Card className="content_inner website__ border-0 custom-card shadow-none">
                            <Card.Body className='p-0 d-flex w-100'>
                                <CardAside filterToggle={filterToggle} closeAside={this.closeAside}>
                                    <Card className={`a_header_ a_footer_ rounded-0`}>
                                        {/* {shopifyAppKey ? <Card.Header className='py-4'>
                                            <ErrorFound> <WebsiteSwitcher contentList={websiteData?.websiteList} changeWebsite={this.changeWebsite} selectedContent={selectedWall?.wall} title="Website" isWebsite={true} /></ErrorFound>
                                        </Card.Header> : null} */}
                                        <Card.Body className='aside_body p-0' style={{ marginBottom: -1}}>
                                            {!footer_action1 && selectedWall?.wall?.showSwitchGallery ? <>
                                                <div className='border-bottom p-4 select-gal-custom'>
                                                    <h5>{content?.title}</h5>
                                                    <ErrorFound> <ContentSwitcher contentList={allWalls} modifyContent={(data) => this.ChangeGallery(data.id)} selectedContent={activeGallery} title="Website" onlySwitcher={true} selectLoader={selectLoader} isWeb={true}/></ErrorFound>
                                                </div>
                                                {/* <Nav variant="pills" className="f-center my-2" activeKey={socialReview} onSelect={event => this.changeThemeSection(event)}>
                                                    <Nav.Item>
                                                        <Nav.Link className="border btn btn-primary mx-1 py-2" eventKey="1">Social</Nav.Link>
                                                    </Nav.Item>
                                                    <Nav.Item className="position-relative">
                                                        <Nav.Link className="border btn btn-primary mx-1 py-2" eventKey="2">Reviews</Nav.Link>
                                                        <span className="position-absolute badge-primary fs-10 border border-2 border-light badge m-0 badge" style={{ top: '-7px', right: '-14px', padding: '2px' }}>New</span>
                                                    </Nav.Item>
                                                </Nav> */}
                                                </>
                                                : ''}
                                            {footer_action1 ?
                                                <PlatformAside changeGalleryType={this.changeGalleryType} galleryType={galleryType} changePlatform={this.changePlatform} wallData={appData?.wall} platform={platform} isShopify={shopifyAppKey} isShoppable={authorized?.userData?.user_data?.UserRule?.shoppableStatus} content={content} />
                                                : ''}
                                            {footer_action2 ?
                                                <Tab.Container defaultActiveKey="theme">
                                                    <NavOption isShoppable={authorized?.userData?.user_data?.UserRule?.shoppableStatus} themeId={appData?.wall?.ThemeInfo?.id}/>
                                                    <Tab.Content>
                                                        <Tab.Pane eventKey="theme">
                                                            <ErrorFound><ThemeList {...{bannerData, socialReview, content: content['theme']}} themeList={themeData.themeList} refreshEmbed={this.refreshEmbed} selectedContent={activeGallery} sectionChanged={sectionChanged} changeThemeSection={this.changeThemeSection} resetSection={() => this.setState({ sectionChanged: false })} /></ErrorFound>
                                                        </Tab.Pane>
                                                        <Tab.Pane eventKey="customization">
                                                            {appData?.wall && !changeSetting && <ErrorFound><Customization {...{socialReview}} a_key={activeKey} bannerDetail={this.props?.bannerDetail} appData={this.props?.appData} fonts={allFont} content={content['customization']} /></ErrorFound>}
                                                        </Tab.Pane>
                                                        <Tab.Pane eventKey="cta">
                                                            {appData?.wall ? <ErrorFound><CTAButtonStyle content={content['customization']} /></ErrorFound> : ''}
                                                        </Tab.Pane>
                                                    </Tab.Content>
                                                </Tab.Container> : ''}
                                        </Card.Body>
                                        {<Card.Footer className={`d-flex justify-content-end ${shopifyAppKey ? 'd-md-block d-none' : ''}`}>
                                            {footer_action2 ?
                                                <div className="f-between w-100 gap-3">
                                                    {/* <Button variant='link text-nowarp' id='discardSetting' onClick={() => this.discardSetting()}>
                                                    <FontAwesomeIcon icon={'arrow-left'} iconClass="me-1" />
                                                    Discard
                                                </Button> */}
                                                    {(appData?.is_wall_change || appData?.is_themes_change || appData?.is_backgroun_change || appData?.is_banner_IMG_change || appData?.is_banner_change || appData?.is_card_style_change || appData?.is_theme_setting_change || appData?.is_custome_css_change || appData?.is_magic_upload_change || appData?.is_cta_setting_change || appData?.is_utm_tracker_change || appData?.isHeaderUpdate || appData?.is_card_cta_change || appData?.is_hash_changed) ?
                                                        <Button id='saveSetting' variant='secondary' className={`${this.state.spinner ? 'spinner' : ''} btn btn-outline w-100`} onClick={this.handleSaveClick} onMouseDown={this.handleMouseDown}>
                                                            {content?.save}
                                                        </Button>
                                                        : null}
                                                    <Button variant='primary' id='discardSetting' className="w-100" onClick={this.handleGenClick} onMouseDown={this.handleGenMouseDown} >
                                                    {content?.btn_generate}
                                                    </Button>
                                                </div>
                                                : ''}
                                            {footer_action3 ?
                                                <div className='my-1 w-100'>
                                                    <Button className='w-100' onClick={() => this.setState({ footer_action1: false, footer_action2: true, footer_action3: false }, () => scrolldiv('.select-gal-custom'))}>
                                                    {content?.btn_back}
                                                    </Button>
                                                </div>
                                                : ''}
                                        </Card.Footer>}
                                        {/* {shopifyAppKey && footer_action3 && <Card.Footer className='d-flex justify-content-end'>
                                            <div className='my-1 w-100'>
                                                <Button className='w-100' onClick={() => this.setState({ footer_action1: false, footer_action2: true, footer_action3: false })}>
                                                    {content?.btn_back}
                                                </Button>
                                            </div>
                                        </Card.Footer>} */}
                                    </Card>
                                </CardAside>

                                <Card className={`custom-preview border-0 preview_ ${footer_action1 ? 'custom-schema-main p_header_' : 'p_footer_'}`}>
                                    <Button id="toggle_aside" variant='primary' className={`f-center position-absolute end-0 top-0 rounded-pill m-3 ${shopifyAppKey ? 'd-md-none' : 'd-xl-none'}`} onClick={() => this.setState({ filterToggle: !filterToggle })} style={{ zIndex: 9 }}>
                                        <FontAwesomeIcon icon={'sliders'} iconClass="me-1" />
                                        {emailCampaign?.mobileMenuButton ?? ""}
                                    </Button>
                                    {footer_action1 ?
                                        <Tab.Container activeKey={previewKey} onSelect={event => this.setState({ previewKey: event })}>
                                            {galleryType !=4 && <Card.Header className='py-0 align-items-center justify-content-center border-bottom'>
                                                <Nav className='gap-0'>
                                                    <Nav.Item className=''>
                                                        <Nav.Link eventKey="script" className={`${previewKey === 'script' ? 'btn-primary' : 'btn-outline-primary border-end-0'} btn rounded-0 rounded-start`}>
                                                            <FontAwesomeIcon icon="code" className="me-2" />
                                                            {content?.script}
                                                        </Nav.Link>
                                                    </Nav.Item>
                                                    <Nav.Item className=''>
                                                        <Nav.Link eventKey="preview" className={`${previewKey === 'preview' ? 'btn-outline-primary' : 'btn-primary border-start-0'} btn rounded-0 rounded-end`}>
                                                            <FontAwesomeIcon icon="eye" className="me-2" />
                                                            {content?.preview}  
                                                        </Nav.Link>
                                                    </Nav.Item>
                                                </Nav>
                                            </Card.Header>}
                                            <Card.Body>
                                                <Tab.Content>
                                                    <Tab.Pane eventKey="script">
                                                        <ErrorFound>
                                                            <GenerateCode key={localStorage.getItem('website_id')} galleryType={galleryType} platform={platform} closePopup={this.closePopup} isShopify={shopifyAppKey} isBigCommerce={isBigCommerce} wallData={this.props?.appData?.wall} isShoppable={authorized?.userData?.user_data?.UserRule?.shoppableStatus} content={content['platform']}/>
                                                        </ErrorFound>
                                                    </Tab.Pane>
                                                    <Tab.Pane eventKey="preview">
                                                        <div className='theme_preview_wrap'>
                                                            <Suspense fallback={<></>}>
                                                                <ErrorFound><ThemePreview webId={localStorage.getItem('website_id')} key={previewKey} themeId={appData?.wall?.ThemeInfo?.id} galleryType={galleryType} allTags={allTags}/></ErrorFound>
                                                            </Suspense>
                                                        </div>
                                                    </Tab.Pane>
                                                </Tab.Content>
                                            </Card.Body>
                                        </Tab.Container>
                                        : ''}
                                    {footer_action2 ? <>
                                        <Card.Body className='position-relative'>
                                            <div className={`desktop-mob d-none ${shopifyAppKey ? 'd-md-block' : 'd-lg-block'}`}>
                                                <div className="d-flex align-items-center justify-content-center pb-3 pt-1">
                                                    <Button
                                                        variant={this.state.viewMode === 'desktop' ? 'primary rounded-0 rounded-start' : 'outline-primary rounded-0 border-end-0 rounded-start'}
                                                        onClick={() => this.handleViewChange('desktop')}
                                                    >
                                                        <FontAwesomeIcon icon="desktop" className="me-2" />
                                                        {content?.desktop}
                                                    </Button>
                                                    <Button
                                                        variant={this.state.viewMode === 'mobile' ? 'primary rounded-0 rounded-end' : 'outline-primary rounded-0 border-start-0 rounded-end'}
                                                        onClick={() => this.handleViewChange('mobile')}
                                                    >
                                                        <FontAwesomeIcon icon="mobile-alt" className="me-2" />
                                                        {content?.mobile}
                                                    </Button>
                                                </div>
                                            </div>
                                            <div className={`preview-container${this.state.viewMode === 'mobile' ? ' mobile-view' : ''}`}>
                                                {appData?.live_preview_loader || appData?.isPostLoading ? <Loader bg={'body'} /> : ''}
                                                <ErrorFound>
                                                    <Theme key={isUpdate} webId={websiteData?.websiteList.length > 0 ? websiteData?.websiteList[0]?.wall?.ThemeInfo?.webId : ""} isDemo={appData?.demoData} ownerId={authorized?.userData?.user_data?.user?.id} themeId={appData?.wall?.ThemeInfo?.id} appData={appData}/>
                                                </ErrorFound>
                                                {!appData?.live_preview_loader && !appData?.isPostLoading ? <>
                                                    {appData?.error_code && appData?.error_code > 0 && !(!appData?.post_data?.length && appData?.wall?.SnapUp?.Form?.status && appData?.wall?.ThemeInfo?.ownerId>=143090) ? <ErrorFound><ErrorPage errorCode={appData?.error_code} wall={appData?.wall} /></ErrorFound> : <ErrorFound><ThemeConditionError appData={appData} /></ErrorFound>}
                                                </> : null}
                                                {allWalls && allWalls.length <= 0 && !appData?.live_preview_loader && !appData?.isPostLoading && appData?.error_code <= 0 ?
                                                    <div className="f-center h-100">
                                                        <EmptyWithIcon title={emailCampaign?.noContentTitle ?? ""} desc={emailCampaign?.noContentDescription ?? ""} icon={'media'} button={{ action: () => this.props.navigate(`/content/moderation/${localStorage.getItem('active_wall')}`), text: emailCampaign?.addContentButton ?? "" }} isFullScreen={false} />
                                                    </div>
                                                    : null}
                                            </div>
                                        </Card.Body>
                                        {shopifyAppKey ? <Card.Footer className={`${shopifyAppKey ? 'd-md-none' : ''}`}>
                                            {/* <div className='f-center justify-content-end'> */}
                                                {/* <Button variant='primary' className='mx-4 my-2' onClick={this.handleGenClick} onMouseDown={this.handleGenMouseDown}>
                                                <FontAwesomeIcon icon={'bolt'} iconClass="me-1" />
                                                {content?.btn_generate}
                                            </Button> */}
                                               {/* change update and save btn */}
                                                <div className="d-flex align-items-center justify-content-end w-100">
                                                    {(appData?.is_wall_change || appData?.is_themes_change || appData?.is_backgroun_change || appData?.is_banner_IMG_change || appData?.is_banner_change || appData?.is_card_style_change || appData?.is_theme_setting_change || appData?.is_custome_css_change || appData?.is_magic_upload_change || appData?.is_cta_setting_change || appData?.is_utm_tracker_change || appData?.isHeaderUpdate || appData?.is_card_cta_change || appData?.is_hash_changed) ?
                                                        <Button id='saveSetting' variant='secondary' className={`${this.state.spinner ? 'spinner' : ''} btn btn-outline-primary`} onClick={this.handleSaveClick} onMouseDown={this.handleMouseDown}>
                                                            {content?.save}
                                                        </Button>
                                                        : null}
                                                    <Button variant='primary' className='mx-4 my-2' onClick={this.handleGenClick} onMouseDown={this.handleGenMouseDown}>
                                                <FontAwesomeIcon icon={'bolt'} iconClass="me-1" />
                                                {content?.btn_generate}
                                            </Button>
                                                </div>
                                            {/* </div> */}
                                    </Card.Footer> : null}
                                    </> : null}
                                </Card>
                            </Card.Body>
                        </Card>
                        :
                        websiteData?.websiteLoading ?
                            <WebsiteSkel />
                            :
                            <CreateWebsite toggle={this.props.toggle} TOGGLE_ADD_WEBSITE={this.props.TOGGLE_ADD_WEBSITE} />
                    }
                </Container>
            </div>
        </>
    }
}


const mapStateToProps = (state) => {
    return {
        authorized: state.authorized,
        allWalls: state.dashboardData?.dashboarddata?.allWalls,
        bannerDetail: state.appsMore,
        fonts: state.appsMore.fonts,
        themeData: state.themeData,
        ctaData: state.ctaData.data,
        allFont: state.allFont?.allFonts,
        websiteData: state.websiteList,
        appData: state.appData,
        toggle: state.websiteList.addWebsiteNow,
        allTags: state.allTags?.selectedTags
    }
}

const mapDispatchToProps = dispatch => {

    return {
        GET_WALL_DETAILS: () => dispatch(GET_WALL_DETAILS()),
        GET_THEME_LIST: () => dispatch(GET_THEME_LIST()),
        getFontVariants: () => dispatch(getFontVariants()),
        GET_CTA_ACTION: (wallData) => dispatch(GET_CTA_ACTION(wallData)),
        GET_APP_DATA: (data, callBack) => dispatch(CHANGE_WEBSITE(data, callBack)),
        CHANGE_WEBSITE_WALL: (Id, limit, themeId) => dispatch(CHANGE_WEBSITE_WALL(Id, limit, themeId)),
        SAVE_AND_PUBLISH: (status) => dispatch({ type: SAVE_AND_PUBLISH, payload: status }),
        SET_ALL_UPDATED: () => dispatch({ type: SET_ALL_UPDATED, payload: false }),
        getUserInfo_afterChanges: () => dispatch(getUserInfo_afterChanges()),
        TOGGLE_ADD_WEBSITE: () => dispatch({ type: TOGGLE_ADD_WEBSITE, payload: true }),
        GET_ALL_WEBSITE_LIST: () => dispatch(GET_ALL_WEBSITE_LIST()),
        SNAPUP_DETAILS_ACTION: () => dispatch(SNAPUP_DETAILS_ACTION()),
        ACTIVE_WEBSITE_ID: () => dispatch({ type: ACTIVE_WEBSITE_ID_REDUCER, payload: localStorage.getItem('website_id') }),
        WALL_CHANGED: () => dispatch({ type: IS_WALL_CHANGE, payload: true }),
        storeCallback: (callback) => dispatch({type:CALLBACK_STORE, payload:callback})
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Website));