import { useRef, useState, useMemo, useEffect } from "react";
import moment from 'moment';
import Select from "react-select";
import { useDispatch } from 'react-redux';
import { Button, Form, OverlayTrigger, Tooltip } from "react-bootstrap";
import RangeInput from "../../CommonComponents/RangeInput";
import { FontAwesomeIcon } from "../../../../../Assets/AssetsHelper";
import { refreshAiSummary, updateThemeSetting } from "../../../../../actions/appsMoreAction";
import { COLUMNLIST, TOAST_ERROR, TOAST_SUCCESS, validURL } from "../../../../../utils";
import DateTimeField from '@1stquad/react-bootstrap-datetimepicker';
import '@1stquad/react-bootstrap-datetimepicker/css/bootstrap-datetimepicker.css';
import { SETTING_AUTO_SLIDER, SETTING_VIDEO_PLAY, SETTING_BEST_FIT, SETTING_CAROUSEL_POST, SETTING_COLUMNS, SETTING_COLUMNS_MOBILE, SETTING_MINIMUM_POSTWIDTH, SETTING_NO_OF_ROW, SETTING_POST_NUMBER, SETTING_POST_SPACING, SETTING_SHOW_MORE_BTN, SETTING_TIME_PER_SLIDER, SETTING_TRIM_CONTENT, SETTING_POST_TEXT, SETTING_HIDE_POST, SETTING_HIDE_OLDER_POST, SETTING_SHUFFLE_POST, SETTING_DUPLICATE_POST, SETTING_FEATURE_POPUP, SETTING_AI_SUMMARY, SETTING_CUSTOM_URL, SETTING_MOBILE_POPUP, IS_THEME_SETTING_CHANGE, SETTING_HIDE_POST_DATE } from "../../../../../reducer/keys";
import CustomToggleButton from "./Element/CustomToggleButton"
import { CHANGE_WEBSITE_WALL } from "../../../../../actions/websiteAction";

const ThemeSetting = (props) => {
  const { ThemeInfo, ThemeStyle, CardStyle } = props.wallData
  const themeId = ThemeInfo?.id
  const themeContent = props.content['more_actions'];
  const dispatch = useDispatch()

  const { hidePostRange, rowSelect, popupList, AiSummary, TickerSlider, columnOptions, mobColumnOptns, showMoreList, autoLoadList } = useMemo(() => {
    return {
      hidePostRange: themeContent?.hide_post_range ?? [],
      rowSelect: themeContent?.rows_options ?? [],
      popupList: themeContent?.pop_up_actions ?? [],
      AiSummary: themeContent?.ai_summary_styles ?? [],
      TickerSlider: themeContent?.ticker_slider_speed ?? [],
      columnOptions: COLUMNLIST(themeId, false),
      mobColumnOptns: COLUMNLIST(themeId, true),
      showMoreList: [50, 4, 19, 47, 3, 85, 5, 83, 20, 49, 84, 79],
      autoLoadList: [61, 62, 16, 86, 60, 52, 81, 80]
    }
  }, [themeContent, themeId])

  const [urlError, setUrlError] = useState(false)
  const [isSummary, setIsSummary] = useState(false)
  const [isFilter, setIsFilter] = useState(0)

  const myRefname = useRef(null);

  const hideOldPostDate = useMemo(() => {
    const date = (ThemeStyle?.oldPost?.showHideOldPost ?? 0);
    const isValid = moment(date, moment.ISO_8601, true).isValid();
    const dateToFormat = isValid ? moment(date) : moment();
    return dateToFormat.format("MM DD YYYY hh:mm");
  }, [ThemeStyle?.oldPost])

  useEffect(() => {
    if (ThemeStyle?.filterStatus) setIsFilter(ThemeStyle?.filterStatus)
  }, [ThemeStyle?.filterStatus])

  useEffect(() => {
    if (props.save_and_publish == true && props.is_theme_setting_change == true) {
      setTimeout(() => { submitData(); }, 500);
    }
  }, [props.save_and_publish]);

  const submitData = () => {
    let data = {
      websiteId: localStorage.getItem('website_id'),
      wallId: localStorage.getItem('active_wall'),
      p_id: ThemeStyle?.id,
      carouselPost: Number(ThemeStyle?.carouselPost) ?? 1,
      cssStatus: ThemeStyle?.css.status,
      randomizePost: ThemeStyle?.randomizePost,
      loadMoreStatus: Number(ThemeStyle?.loadMoreStatus),
      autoScrollStatus: Number(ThemeStyle?.autoScrollStatus),
      postText: Number(ThemeStyle?.textPostStatus),
      mobilePopup: Number(ThemeStyle?.modal.mobile),
      trimcontent: Number(CardStyle?.content.trimcontent) || 0,
      popupSlideShow: 1,
      textDecorate: Number(CardStyle?.style.textDecoration),
      customUrl: `${ThemeStyle?.modal.redirect}`,
      numberOfPosts: ThemeStyle?.totalPosts,
      hidePosts: ThemeStyle?.oldPost.status,
      showHideOlderPost: ThemeStyle?.oldPost.showFrom,
      showHideOldPost: ThemeStyle?.oldPost.showHideOldPost,
      minimumPostWidth: CardStyle?.minimumWidth,
      padding: CardStyle?.style.margin,
      theme_id: ThemeInfo?.id,
      numberOfColumn: ThemeStyle?.column.desktop,
      mobileColumn: ThemeStyle?.column.mobile,
      multiRow: ThemeStyle?.row,
      fitRow: Number(ThemeStyle?.bestFit),
      autoPlay: Number(ThemeStyle?.autoPlay),
      slidePost: Number(ThemeStyle?.slide.autoSlide),
      slideDuration: ThemeStyle?.slide.slideDuration,
      postFeatured: Number(ThemeStyle?.modal?.action),
      duplicatePost: ThemeStyle?.duplicatePost,
      filterStatus: isFilter || 0,
      ai_summary: ThemeStyle?.aiSummaryStatus,
    }

    if ((ThemeStyle?.modal.action == 3 && validURL(ThemeStyle?.modal.redirect)) || ThemeStyle?.modal.action != 3) {
      updateThemeSetting(data).then(res => {
        // const { data } = res
        // if (data.status) { dispatch(GET_ALL_WEBSITE_LIST()) }
      }).catch((error) => console.warn(error))
    }
    
  }

  const summarizedPost = () => {
    setIsSummary(true)
    refreshAiSummary(ThemeInfo?.wallId).then(response => {
      TOAST_SUCCESS('Ai summary refreshed successfully!');
      setIsSummary(false)
    }).catch((error) => {
      TOAST_ERROR('Failed to summarize media.Please try again.')
      setIsSummary(false)
    })
  }

  return (<>
    {/* <div className="mt-4">
      <CustomToggleButton toggleName="Popup 'slider'" toggleValue={ThemeStyle?.modal.slide} callbackFn={() => { }} callbackFn2={() => { }} dispatchfn={(status) => dispatch({ type: SETTING_POPUP_SLIDER, payload: Number(status) })} tooltipTxt=" This feature lets you slide through the pop-up." />
    </div> */}

    {![87, 82].includes(themeId) ? <div className="mt-4">
      <CustomToggleButton toggleName={themeContent.carousel_post} callbackFn2={() => { }} toggleValue={ThemeStyle?.carouselPost} callbackFn={() => { }} dispatchfn={(status) => dispatch({ type: SETTING_CAROUSEL_POST, payload: Number(status) })} tooltipTxt={themeContent.carousel_post_tooltip} />
    </div> : null}

    {![62, 82].includes(themeId) ? <div className="mt-4">
      <CustomToggleButton toggleName={themeContent.shuffle_post} callbackFn2={() => { }} toggleValue={ThemeStyle?.randomizePost} callbackFn={() => { }} dispatchfn={(status) => dispatch({ type: SETTING_SHUFFLE_POST, payload: Number(status) })} tooltipTxt={themeContent.shuffle_post_tooltip} />
    </div> : null}

    {![62, 85, 82].includes(themeId) ? <div className="mt-4">
      <CustomToggleButton toggleName={themeContent.hide_duplicate_post} callbackFn2={() => { }} toggleValue={ThemeStyle?.duplicatePost} callbackFn={() => { }} dispatchfn={(status) => dispatch({ type: SETTING_DUPLICATE_POST, payload: Number(status) })} tooltipTxt={themeContent.hide_duplicate_post_tooltip} />
    </div> : null}

    {[50, 4, 19, 3, 85, 5, 83, 20, 49, 47, 79].includes(themeId) && ThemeStyle?.filterStatus ? <div className="mt-4">
      <CustomToggleButton toggleName={themeContent.web_embed_filter} callbackFn2={() => { }} toggleValue={isFilter} callbackFn={(status) => { setIsFilter(Number(status)) }} dispatchfn={(status) => dispatch({ type: IS_THEME_SETTING_CHANGE, payload: true })} tooltipTxt={themeContent.web_embed_filter_tooltip} />
    </div> : null}


    {[16, 61, 62, 86, 60, 52, 81, 80, 47, 88].includes(themeId) ?
      <div className="mt-4">
        <CustomToggleButton toggleName={themeContent.auto_slide} callbackFn2={() => { }} toggleValue={ThemeStyle?.slide.autoSlide} callbackFn={() => { }} dispatchfn={(status) => dispatch({ type: SETTING_AUTO_SLIDER, payload: Number(status) })} tooltipTxt={themeContent.auto_slide_tooltip} />
      </div> : ''}

    {!([63, 64, 85, 87, 80, 82, 84, 79]).includes(themeId) ? <div className="mt-4">
      <CustomToggleButton toggleName={themeContent.video_autoplay} callbackFn2={() => { }} toggleValue={ThemeStyle?.autoPlay} callbackFn={() => { }} dispatchfn={(status) => dispatch({ type: SETTING_VIDEO_PLAY, payload: Number(status) })} tooltipTxt={themeContent.video_autoplay_tooltip} />
    </div> : null}
    {showMoreList.includes(themeId) ?
      <div className="mt-4">
        <CustomToggleButton toggleName={themeContent.show_more_button} toggleValue={ThemeStyle?.loadMoreStatus} callbackFn={() => { }} callbackFn2={() => { }} dispatchfn={(status) => dispatch({ type: SETTING_SHOW_MORE_BTN, loadMore: Number(status), autoLoad: Number(ThemeStyle?.autoScrollStatus ? false : ThemeStyle?.autoScrollStatus) })} tooltipTxt={themeContent.show_more_button_tooltip} />
      </div> : null}
    {showMoreList.includes(themeId) || autoLoadList.includes(themeId) ?
      <div className="mt-4">
        <CustomToggleButton toggleName={themeContent.auto_load} toggleValue={ThemeStyle?.autoScrollStatus} callbackFn={() => { }} callbackFn2={() => { }} dispatchfn={(status) => dispatch({ type: SETTING_SHOW_MORE_BTN, autoLoad: Number(status), loadMore: showMoreList.includes(themeId) ? Number(ThemeStyle?.loadMoreStatus ? false : ThemeStyle?.loadMoreStatus) : 0 })} tooltipTxt={themeContent.auto_load_tooltip} />
      </div> : null}
    {![62, 86, 61, 16, 4, 50, 52, 60, 81, 88].includes(themeId) ?
      <div className="mt-4">
        <CustomToggleButton toggleName={themeContent.hide_posts} callbackFn2={() => { }} toggleValue={ThemeStyle?.textPostStatus} callbackFn={() => { }} dispatchfn={(status) => dispatch({ type: SETTING_POST_TEXT, payload: status })} tooltipTxt={themeContent.hide_posts_tooltip} />
      </div> : ''}
    {[0].includes(themeId) ?
      <div className="mt-4">
        <CustomToggleButton toggleName={themeContent.trim_content} callbackFn2={() => { }} toggleValue={CardStyle?.content.trimcontent} callbackFn={() => { }} dispatchfn={(status) => dispatch({ type: SETTING_TRIM_CONTENT, payload: Number(status) })} tooltipTxt={themeContent.trim_content_tooltip} />
      </div> : ''}

    {[52, 84].includes(themeId) ?
      <div className="mt-4">
        <CustomToggleButton toggleName={themeContent.mobile_popup} callbackFn2={() => { }} toggleValue={ThemeStyle?.modal?.mobile} callbackFn={() => { }} dispatchfn={(status) => dispatch({ type: SETTING_MOBILE_POPUP, payload: Number(status) })} tooltipTxt={themeContent.mobile_popup_tooltip} />
      </div> : ''}

    {themeId == 4 ?
      <div className="mt-4">
        <CustomToggleButton toggleName={themeContent.best_fit} callbackFn2={() => { }} toggleValue={ThemeStyle?.bestFit} callbackFn={() => { }} dispatchfn={(status) => dispatch({ type: SETTING_BEST_FIT, payload: Number(status) })} tooltipTxt={themeContent.best_fit_tooltip} />
      </div> : ''}

    {![82].includes(themeId) ? <div className="mt-4">
      <Form.Label className="mb-2" htmlFor={`onclick_post`}>{themeContent.on_click}</Form.Label>
      <Select id="onclick_post" className={`selectpicker selectpicker-sm`} classNamePrefix="selectpicker" options={popupList}
        value={popupList[popupList.findIndex(i => i.value == ThemeStyle?.modal?.action)]}
        onChange={e => dispatch({ type: SETTING_FEATURE_POPUP, payload: e.value ? e.value : 0 })} />
    </div> : null}

    {ThemeStyle?.modal.action == 3 ? <div className="mt-4">
      <Form.Label className="mb-2" htmlFor="url_text">{themeContent.custom_url}</Form.Label>
      <Form.Control id="url_text" className={urlError ? 'is-invalid' : ''} value={ThemeStyle?.modal.redirect}  onChange={e => { dispatch({ type: SETTING_CUSTOM_URL, payload: e.target.value }); setUrlError(!validURL(e.target.value)) }} placeholder="https://www.google.com/" />
      {urlError && <span className='invalid-feedback'>Please enter valid url.</span>}
    </div> : null}

    {[80, 79, 85, 84, 20, 5, 19, 47, 49, 83].includes(themeId) ? <div className="mt-4">
      <Form.Label className="mb-2" htmlFor={`aiSummary`}>
        <span className='me-2'>{themeContent.ai_summarizer}</span>
        <OverlayTrigger placement='top' overlay={<Tooltip>{themeContent.ai_summarizer_tooltip}</Tooltip>} >
          <span><FontAwesomeIcon type={'solid'} icon={'circle-info'} iconClass={'fs-8 text-muted me-2'} /></span>
        </OverlayTrigger>
        {ThemeStyle?.aiSummaryStatus == 1 || ThemeStyle?.aiSummaryStatus == 2 ? <OverlayTrigger placement="top" overlay={<Tooltip>Refresh</Tooltip>}>
          <Button variant='icon' size='sm' className="btn-active-primary" aria-label='refresh' onClick={summarizedPost}>
            <span className={`d-inline-block ${isSummary ? "rotate-animation" : ''} `}><FontAwesomeIcon icon={'arrows-rotate'} /></span>
          </Button>
        </OverlayTrigger> : null}
      </Form.Label>
      <Select id="aiSummary" className={`selectpicker selectpicker-sm`} classNamePrefix="selectpicker" options={AiSummary}
        value={AiSummary[AiSummary.findIndex(i => i.value == ThemeStyle?.aiSummaryStatus)]}
        onChange={e => dispatch({ type: SETTING_AI_SUMMARY, payload: e.value ? e.value : 0 })} />
    </div> : null}

    {![82].includes(themeId) ? <div className="mt-4">
      <Form.Label className="mb-2 d-flex flex-wrap" htmlFor={`total_post`}>
        <span className='me-2'>{themeContent.number_of_posts}</span>
        <OverlayTrigger placement='top' overlay={<Tooltip>{themeContent.number_of_posts_tooltip}</Tooltip>} >
          <span><FontAwesomeIcon type={'solid'} icon={'circle-info'} iconClass={'fs-8 text-muted'} /></span>
        </OverlayTrigger>
      </Form.Label>
      <Form.Control type="number" id={`total_post`} min="1" max="100" value={ThemeStyle?.totalPosts} onChange={(e) => { dispatch({ type: SETTING_POST_NUMBER, payload: e.target.value > 100 ? 100 : e.target.value <= 0 ? 1 : e.target.value }) }} />
    </div> : null}
 

    {[87].includes(themeId) ?
      <div className="mt-4">
        <Form.Label className="mb-2" htmlFor={`ticker_slide`}>{themeContent.slider_speed} </Form.Label>
        <Select id="ticker_slide" className={`selectpicker selectpicker-sm`} classNamePrefix="selectpicker" options={TickerSlider} value={TickerSlider[TickerSlider.findIndex(i => i.value == ThemeStyle?.slide.slideDuration)]}
          onChange={e => { dispatch({ type: SETTING_TIME_PER_SLIDER, payload: e.value }) }} />
      </div>
      : null}

    {[61, 16, 60, 52, 81, 80, 47, 62, 86, 88].includes(themeId) & ThemeStyle?.slide.autoSlide ?
      <div className="mt-4">
        <Form.Label className="mb-2" htmlFor={`time_slide`}>{themeContent.time_per_slide} ({ThemeStyle?.slide.slideDuration} second)</Form.Label>
        <RangeInput id="time_slide" min={1} max={15} step={1} value={ThemeStyle?.slide.slideDuration} onChange={(event) => { dispatch({ type: SETTING_TIME_PER_SLIDER, payload: event }) }} />
      </div> : null}
    {![19, 85, 87, 52, 49, 82, 84].includes(themeId) ? <>
      <div className="mt-4">
        <Form.Label className="mb-2" htmlFor={`desktop_col`}>{themeContent.column_desktop} </Form.Label>
        <Select id="desktop_col" className={`selectpicker selectpicker-sm`} classNamePrefix="selectpicker" options={columnOptions}
          value={columnOptions[columnOptions.findIndex(i => i.value == ThemeStyle?.column.desktop)]}
          onChange={e => { dispatch({ type: SETTING_COLUMNS, payload: e.value ? e.value : 0 }); dispatch(CHANGE_WEBSITE_WALL(localStorage.getItem('active_wall'), ThemeStyle?.totalPosts)) }} />
      </div>

      {![50].includes(themeId) ? <div className="mt-4">
        <Form.Label className="mb-2" htmlFor={`mobile_col`}>{themeContent.column_mobile} </Form.Label>
        <Select id="mobile_col" className={`selectpicker selectpicker-sm`} classNamePrefix="selectpicker" options={mobColumnOptns} value={mobColumnOptns[mobColumnOptns.findIndex(i => i.value == ThemeStyle?.column.mobile)]}
          onChange={e => { dispatch({ type: SETTING_COLUMNS_MOBILE, payload: e.value }) }} />
      </div> : null}
    </> : null}

    {ThemeStyle?.row && themeId == 16 ?
      <div className="mt-4">
        <Form.Label className="mb-2" htmlFor={`no_of_row`}>{themeContent.no_of_row} ({ThemeStyle?.row})</Form.Label>
        <Select id="mobile_col" className={`selectpicker selectpicker-sm`} classNamePrefix="selectpicker" options={rowSelect} defaultValue={rowSelect[ThemeStyle?.row - 1]} onChange={e => { dispatch({ type: SETTING_NO_OF_ROW, payload: e.value }) }} />
      </div> : null}

    {![62, 82].includes(themeId) ?
      <div className="mt-4">
        <div className="mb-2">
          <CustomToggleButton toggleName={themeContent.hide_old_posts} dispatchfn={(status) => dispatch({ type: SETTING_HIDE_POST, payload: status })} toggleValue={ThemeStyle?.oldPost.status} callbackFn={() => { }} callbackFn2={() => { }} tooltipTxt={themeContent.hide_old_posts_tooltip} />
        </div>
        <div className="mb-2">
          {ThemeStyle?.oldPost.status ? <Select id="hide_post_range" className={`selectpicker selectpicker-sm`} classNamePrefix="selectpicker" menuPlacement="top" options={hidePostRange} defaultValue={hidePostRange[ThemeStyle?.oldPost.showFrom == 30 ? 7 : ThemeStyle?.oldPost.showFrom == 8 ? 8 : ThemeStyle?.oldPost.showFrom - 1]} onChange={e => dispatch({ type: SETTING_HIDE_OLDER_POST, payload: e.value })} /> : null}
        </div>
        {ThemeStyle?.oldPost.showFrom == 8 && ThemeStyle?.oldPost.status ?
          <DateTimeField
            language={'en'}
            dateTime={hideOldPostDate}
            inputFormat={'MM/DD/YYYY HH:mm A'}
            onChange={(e) => dispatch({ type: SETTING_HIDE_POST_DATE, payload: moment(e).unix() * 1000 })}
            viewMode={'date'}
            className="form-control form-control-sm"
            startOfWeek={'week'}
            ref={myRefname}
            defaultText="Select date & time"
            id="hide_post_date"
          /> : null}
      </div> : null}

    {[16, 19, 60, 85, 5, 80, 79].includes(themeId) && (!ThemeStyle?.column?.desktop || ThemeStyle?.column?.desktop == 0) ?
      <div className="mt-4">
        <Form.Label className="mb-2" htmlFor={`min_post_w`}>{themeContent.min_post_w} ({CardStyle?.minimumWidth}px)</Form.Label>
        <RangeInput id="time_slide" min={150} max={500} step={1} value={CardStyle?.minimumWidth} onChange={(event) => { dispatch({ type: SETTING_MINIMUM_POSTWIDTH, payload: event }) }} />
      </div> : null}

    {![87, 52, 49, 82, 84, 88].includes(themeId) ?
      <div className="mt-4">
        <Form.Label className="mb-2" htmlFor={`min_post_w`}>{themeContent.post_spacing} ({CardStyle?.style.margin}px)</Form.Label>
        <RangeInput id="time_slide" min={0} max={50} step={1} value={CardStyle?.style.margin} onChange={(event) => { dispatch({ type: SETTING_POST_SPACING, payload: event }) }} />
      </div> : null}
  </>
  );
};

export default ThemeSetting;