import React, { Component } from 'react'
import ErrorFound from '../../../../ErrorFound'
import InheritSwitch from './Element/InheritSwitch'
import { Form } from 'react-bootstrap'
import ColorPicker from '../../CommonComponents/ColorPicker'
import SelectFont from '../Components/SelectFont'
import { connect } from 'react-redux'
import Select from "react-select";
import AttachImage from '../../CommonComponents/Uploads/AttachImage'
import { BANNER_HEADER_CHANGE, CHANGE_BANNER } from '../../../../../reducer/keys'

class SocialHeader extends Component {

    state={
        networkList:[],
        selectedNetwork: {lable:"All", value: 0},
    }

    componentDidMount(){
        const { wallData} = this.props
        const { Banner } = wallData
        if(Banner?.length > 0) {
            const list = Banner?.map(item=>({ label : item.network.id == 0 ? "All" : item.network.name, value : item.network.id })) || []
            const updateList = list?.length > 1 ? [...this.state.networkList, ...list] : list
            this.setState({
                    networkList: updateList,
                    selectedNetwork: list?.length > 1 ? { label: "All", value: 0 } : list?.[0]
                }
            );
        }
    }

    
    componentDidUpdate(prevProps) {
        const { wallData } = this.props
        if (prevProps?.wallData?.Banner?.length !== wallData?.Banner?.length) {
            const list = wallData?.Banner?.map(item=>({ label : item.network.id == 0 ? "All" : item.network.name, value : item.network.id })) || []
            const updateList = list?.length > 1 ? [...this.state.networkList, ...list] : list
            this.setState({
                    networkList: updateList,
                    selectedNetwork: list?.length > 1 ? { label: "All", value: 0 } : list?.[0]
                }
            );
        }
      }

    updateSocialBanner = (key, key2, key3, value, status = false) => {
        const { wallData, changeBanner, changeBannerHeader } = this.props
        const { selectedNetwork } = this.state
        const data = wallData?.Banner?.find(item=> item.network.id == selectedNetwork.value);

       if(!status) if(value?.trim() == (key3 ? data[key][key2][key3] : data[key][key2])?.trim()) return false

        const isGlobalUpdate = [0, 'font', 'color'].includes(key3);
        let updatedData = wallData?.Banner?.map(item => isGlobalUpdate ? { ...item, [key]: {...item[key], [key2]: key3 ? {...item[key][key2], [key3]: value, [key3 == 'font' && 'fontLink']: key3 == 'font' && value?.replace(/ /g, '+')} : value }} : item?.network?.id === selectedNetwork.value ? { ...item, [key]: {...item[key], [key2]: key3 ? {...item[key][key2], [key3]: value} : value }, userUpdated: 1, wallId: localStorage.getItem('active_wall'), website_id: localStorage.getItem('website_id') } : {...item, wallId: localStorage.getItem('active_wall'), website_id: localStorage.getItem('website_id')}
        );
        changeBanner({updatedData, activeBanner: selectedNetwork.value})
        changeBannerHeader(true);
    }

    render() {
        const { inheritStyle, wallData, content } = this.props;
        const { Banner } = wallData
        const { networkList, selectedNetwork } = this.state
        
        const data = Banner?.find(item=> item.network.id == selectedNetwork?.value)
        const bannerText = content?.['banner_style']
        return <>
            <ErrorFound><InheritSwitch content={content}/></ErrorFound>
            <div>
                <p className="h5">{bannerText?.overall_settings}</p>
            </div>
            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="s_bg_c">{bannerText?.background_color}</Form.Label>
                <ColorPicker id="primaryColor" isDisabled={""} defaultColor={data?.background?.primaryColor} changeColor={(clr) => {this.updateSocialBanner('background', 'primaryColor', 0, clr)}} />
            </div>

            <div className="mt-4">
                <SelectFont inheritStyle={inheritStyle} fontChange={(font) => {this.updateSocialBanner('title', 'style', 'font', font.label.key)}} variantChange={this.onChangeVariant} fontval={data?.title.style.font} vrntVal={data?.title.style.font} content={content} />
            </div>

            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="s_f_c">{content?.font_color}</Form.Label>
                <ColorPicker id="color" isDisabled={inheritStyle ? "disabled" : ""} defaultColor={data?.title.style.color} changeColor={(clr) => {this.updateSocialBanner('title', 'style', 'color', clr)}} inheritStyle={inheritStyle} />
            </div>

            <hr className=""></hr>
            <div>
                <p className="h5">{bannerText?.other_settings}</p>
            </div>

            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor={`s_ntwrk`}>{bannerText?.select_network}</Form.Label>
                <Select id="s_ntwrk" className={`selectpicker selectpicker-sm`} classNamePrefix="selectpicker" options={networkList}
                value={networkList?.length && selectedNetwork ? networkList[networkList?.findIndex(i => i.value == selectedNetwork.value)] : null}
                onChange={e => this.setState({selectedNetwork: e})} />
            </div>

            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="s_t">{bannerText?.title}</Form.Label>
                <Form.Control id="s_t" value={data?.title?.text} maxLength="30" onChange={e => {this.updateSocialBanner('title', 'text', null, e.target.value, true)}} />
            </div>

            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="s_st">{bannerText?.subtitle}</Form.Label>
                <Form.Control id="s_st" value={data?.subTitle?.text} maxLength="30" onChange={e => {this.updateSocialBanner('subTitle', 'text', null, e.target.value, true)}} />
            </div>

            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="s_pi">{bannerText?.profile_image}</Form.Label>
                <AttachImage id="s_pi" imageStyle={{ paddingBottom: '66%' }} backgroundImage={data?.logo?.url} onClickTouploadImage={(url)=> {this.updateSocialBanner('logo', 'url', null, url)}} />
            </div>

            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="s_bt">{bannerText?.button_text}</Form.Label>
                <Form.Control id="s_bt" value={data?.button?.text} maxLength="30" onChange={e => {this.updateSocialBanner('button','text', null, e.target.value, true)}} />
            </div>

            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="s_bt_txt_c">{bannerText?.button_text_color}</Form.Label>
                <ColorPicker id="social" isDisabled={""} defaultColor={data?.button?.style?.color} changeColor={(clr) => {this.updateSocialBanner('button','style','color', clr)}} />
            </div>

            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="s_bt_c">{bannerText?.button_color}</Form.Label>
                <ColorPicker id="secondaryColor" isDisabled={""} defaultColor={data?.button?.style?.background} changeColor={(clr) => {this.updateSocialBanner('button','style', 'background', clr)}} />
            </div>
            {
                selectedNetwork?.value ?
                    <div className="mt-4">
                        <Form.Label className="mb-2" htmlFor="s_t">{bannerText?.follow_us}</Form.Label>
                        <Form.Control id="s_t" value={data?.button?.followLink} onChange={e => { this.updateSocialBanner('button', 'followLink', null, e.target.value) }} />
                    </div>
                    : null
            }
        
        </>
    }
}

const mapStateToProps = (state) => {
    return {
        allFont: state.allFont?.allFonts,
        isHeaderUpdate: state.appData?.isHeaderUpdate,
        inheritStyle: state.appData?.wall?.CardStyle?.inheritStyles,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        changeBanner: (data) => dispatch({type:CHANGE_BANNER, payload:data}),
        changeBannerHeader: (data) => dispatch({type:BANNER_HEADER_CHANGE, payload:data})
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(SocialHeader)
