import { useEffect, useMemo, useState } from "react";
import CodeMirror from "@uiw/react-codemirror";
import { tags as t } from '@lezer/highlight';
import { loadLanguage } from '@uiw/codemirror-extensions-langs';
import { createTheme } from '@uiw/codemirror-themes';
import { Accordion, Button } from "react-bootstrap";
import BackgroundStyle from "./BackgroundStyle";
import { shallowEqual, useDispatch, useSelector } from "react-redux";
import { updateCutomCSS, updateWidgetBanner } from "../../../../../actions/appsMoreAction";
import ThemeSetting from "./ThemeSetting";
import UGCUploads from "./UGCUploads";
import CardStyle from "./CardStyle";
import { TOAST_UPDATE, TOAST_LOADING, OPEN_INTERCOM, showSalesModal } from "../../../../../utils";
import { ACTIVE_VIDEO_POPUP } from "../../../../../reducer/keys";
import { SET_CUSTOM_CSS } from "../../../../../reducer/keys";
import GoogleAnalytics from "./GoogleAnalytic";
import ErrorFound from "../../../../ErrorFound";
import CssSwitch from "./Element/CssSwitch";
import ReviewHeader from "./ReviewHeader";
import CardCTA from "./CardCTA";
import HashHighlight from "./HashHighlight";
import SocialHeader from "./SocialHeader";
import OldHeader from "./OldHeader";
import CustomHeader from "./CustomHeader";
import { FontAwesomeIcon } from "../../../../../Assets/AssetsHelper";
import WebsiteLanguage from "./WebsiteLanguage";
import { updateSalesModal } from "../../../../../actions/loginActions";
import LocalizationPop from "./Element/LocalizationPop";


const Customization = (props) => {
  const { openBanner, a_key, appData, fonts, onCtaChange, socialReview, content } = props
  const [cssOn, setcssOn] = useState(false)
  const [show, setShow] = useState(false)
  const dispatch = useDispatch()
  const { wall } = appData
  let backgroundDetails = wall;
  let onsite = wall?.SnapUp;
  let PersonalizationID = wall?.ThemeStyle?.id // key not exist
  const userData = useSelector((state) => state.authorized?.userData?.user_data || {}, shallowEqual);
  const { shoppableStatus, languageTranslation } = useMemo(()=>{
    return {
      shoppableStatus: userData?.UserRule?.shoppableStatus || false,
      languageTranslation: userData?.UserRule?.languageTranslation || false,
    }
  },[userData])

  useEffect(() => {

    dispatch({ type: ACTIVE_VIDEO_POPUP, payload: 'banner' })

    window.$("#wallThemeRuleCtaStatus").on('change', function (e) {
      onCtaChange()
    })

    window.$("#wallcssStatus").on('change', function (e) {
      setcssOn(this.value == 'true' ? true : false)
    })
  }, [cssOn])

  const applyCss = () => {
    const toastId = TOAST_LOADING()
    updateCutomCSS(wall?.ThemeStyle?.css.code, PersonalizationID).then(res => {
      const { data } = res;
      if (data.status) {
        TOAST_UPDATE(toastId, "success", data.message)
      }
      else TOAST_UPDATE(toastId, "error", data.message)
    }
    ).catch(err => {
      console.log('err', err)
      TOAST_UPDATE(toastId, "error", "Error in updating")
    })
  }

  const myTheme = createTheme({
    theme: 'light',
    settings: {
      background: '#f8f8f8',
      caret: 'none',
      gutterBackground: '#f7f7f7',
    },
    styles: [
      { tag: t.comment, color: '#b06213' },
      { tag: t.variableName, color: '#ff0000' },
      { tag: [t.string, t.special(t.brace)], color: '#b83b3b' },
      { tag: t.number, color: '#166948' },
      { tag: t.bool, color: '#92349f' },
      { tag: t.null, color: '#166948' },
      { tag: t.keyword, color: '#166948' },
      { tag: t.operator, color: '#166948' },
      { tag: t.className, color: '#ff0000' },
      { tag: t.definition(t.typeName), color: '#b83b3b' },
      { tag: t.typeName, color: '#b83b3b' },
      { tag: t.angleBracket, color: '#92349f' },
      { tag: t.tagName, color: '#4d9840' },
      { tag: t.attributeName, color: '#92349f' },
      { tag: t.color, color: '#3a2ba4' },
      { tag: t.color, color: '#3a2ba4' },
    ],
  });

  useEffect(() => {
    if (appData?.save_and_publish == true && appData?.is_custome_css_change == true) applyCss();

    if (appData?.isHeaderUpdate && appData?.save_and_publish == true) updateWidgetBanner(wall?.Banner)
  }, [appData?.save_and_publish, appData?.isHeaderUpdate]);

  return (
    <>
      <Accordion className="border-top" defaultActiveKey="card_style" flush>
        <Accordion.Item eventKey="card_style">
          <Accordion.Header>{content?.card_heading}</Accordion.Header>
          <Accordion.Body className="bg-gray-100">
            {fonts && fonts.length && appData?.wall && Object.keys(appData).length > 0 ?
              <ErrorFound><CardStyle save_and_publish={appData?.save_and_publish} isShoppable={shoppableStatus} wallData={wall} ownerId={wall?.ThemeInfo?.ownerId} fonts={fonts} planID={wall?.UserPlan?.planId} content={content} /></ErrorFound>
              : null}
          </Accordion.Body>
        </Accordion.Item>

        {![82, 84, 87].includes(wall?.ThemeInfo?.id) && appData?.activeBanner ? <Accordion.Item eventKey="header_style">
          <Accordion.Header>{content?.banner_heading}</Accordion.Header>
          <Accordion.Body className="bg-gray-100">
            {fonts && fonts.length && wall && Object.keys(appData).length > 0 ?
              <ErrorFound>{[6, 8].includes(appData?.activeBanner) ? <ReviewHeader content={content} save_and_publish={appData?.save_and_publish} wallData={wall} fonts={fonts} /> : [7].includes(appData?.activeBanner) ? <SocialHeader save_and_publish={appData?.save_and_publish} wallData={wall} ownerId={wall?.ThemeInfo?.ownerId} fonts={fonts} content={content} /> : [2, 3, 4, 5].includes(appData?.activeBanner) ? <OldHeader {...{ wall }} /> : null}</ErrorFound>
              : null}
          </Accordion.Body>
        </Accordion.Item> : null}

        <Accordion.Item eventKey="more_actions">
          <Accordion.Header>{content?.more_actions_heading}</Accordion.Header>
          <Accordion.Body className="bg-gray-100">
            <ErrorFound> <ThemeSetting planID={wall?.UserPlan?.planId} appData={appData} save_and_publish={appData?.save_and_publish} is_theme_setting_change={appData?.is_theme_setting_change} wallData={wall} db_table={wall?.UserPlan?.db_table} adminId={wall} closePopup={openBanner} content={content} /></ErrorFound>
          </Accordion.Body>
        </Accordion.Item>

        <Accordion.Item eventKey="background">
          <Accordion.Header>{content?.background_heading}</Accordion.Header>
          <Accordion.Body className="bg-gray-100">
            <ErrorFound>
              <BackgroundStyle backgroundDetails={backgroundDetails} save_and_publish={appData?.save_and_publish} is_backgroun_change={appData?.is_backgroun_change} PersonalizationID={PersonalizationID} bannerDetail={wall} content={content} />
            </ErrorFound>
          </Accordion.Body>
        </Accordion.Item>

        {(wall?.UserRule?.customCss != 0) ?
          <Accordion.Item eventKey={a_key ? a_key : 'banner'}>
            <Accordion.Header>{content?.custom_css_heading}</Accordion.Header>
            <Accordion.Body className="bg-gray-100">
              <CssSwitch content={content} />
              <div className={`${wall?.ThemeStyle?.css.status ? '' : 'inActiveCustomeCss'}`} >
                <ErrorFound> <CodeMirror value={wall?.ThemeStyle?.css.code} height="350px" save_and_publish={appData?.save_and_publish} theme={myTheme} basicSetup={{ foldGutter: false, dropCursor: false, allowMultipleSelections: false, indentOnInput: false, autocompletion: false }} extensions={[loadLanguage('css')]} onChange={(e) => { dispatch({ type: SET_CUSTOM_CSS, payload: e }) }} editable={wall?.ThemeStyle?.css.status ? true : false} /></ErrorFound>
              </div>
            </Accordion.Body>
          </Accordion.Item> : null}

        <Accordion.Item eventKey="hash_highlight">
          <Accordion.Header>{content?.hash_highlighter_heading}</Accordion.Header>
          <Accordion.Body className="bg-gray-100">
            <ErrorFound><HashHighlight wall={wall} save_and_publish={appData?.save_and_publish} content={content} /></ErrorFound>
          </Accordion.Body>
        </Accordion.Item>

        <Accordion.Item eventKey="google_analytics">
          <Accordion.Header>{content?.google_analytics_heading}</Accordion.Header>
          <Accordion.Body className="bg-gray-100">
            <ErrorFound><GoogleAnalytics wallData={appData} save_and_publish={appData?.save_and_publish} is_utm_tracker_change={appData?.is_utm_tracker_change} content={content} /></ErrorFound>
          </Accordion.Body>
        </Accordion.Item>

        {!shoppableStatus && wall?.CardStyle?.globalCTA?.status && [19, 47, 83, 20, 5, 52, 49, 3, 85].includes(wall?.ThemeInfo?.id) ? <Accordion.Item eventKey="cta">
          <Accordion.Header className="cta_header">{content?.cta_heading}</Accordion.Header>
          <Accordion.Body className="bg-gray-100">
            <ErrorFound> <CardCTA planID={wall?.UserPlan?.planId} appData={appData} save_and_publish={appData?.save_and_publish} is_cta_change={appData?.is_card_cta_change} wallData={wall} db_table={wall?.UserPlan?.db_table} content={content} /></ErrorFound>
          </Accordion.Body>
        </Accordion.Item> : null}

        {onsite?.ruleStatus && !([63, 64].includes(wall?.ThemeInfo?.id)) ?
          <Accordion.Item eventKey="snap_up">
            <Accordion.Header>{content?.snap_up_heading}</Accordion.Header>
            <Accordion.Body className="bg-gray-100">
              <ErrorFound><UGCUploads SnapUp={onsite} save_and_publish={appData?.save_and_publish} is_magic_upload_change={appData?.is_magic_upload_change} PersonalizationID={PersonalizationID} content={content}/></ErrorFound>
            </Accordion.Body>
          </Accordion.Item> : null}

          <Accordion.Item eventKey="website_language" onClick={languageTranslation ? null : () => setShow(true)}>
            <Accordion.Header>{content?.localization ?? ""}<span style={{top: "0px"}} className="comingSoonPrice">{window?.APP_CONTENT?.common?.new ?? "New"}</span></Accordion.Header>
            {languageTranslation ? <Accordion.Body className="bg-gray-100">
              <ErrorFound><WebsiteLanguage wallData={wall} content={content} /></ErrorFound>
            </Accordion.Body> : null}
          </Accordion.Item>

      </Accordion>
      <div className="my-3 mx-2">
        <span className="d-none d-sm-flex align-items-center text-muted py-1 px-3 rounded-pill badge-light-primary flex-wrap fs-9 justify-content-center">
          <span className="d-flex flex-wrap align-items-center">
          {content?.need_help} <strong className="ms-1 me-1"> {content?.we_here}</strong>
            {/* <Button variant='link' className='f-center ms-2 d-flex' onClick={event => setIsShowVideo(!isShowVideo)}>
              <FontAwesomeIcon type={'solid'} icon={'play'} iconClass='me-1' />
              Watch Video
            </Button> */}
          </span>
          {/* <span className='mx-1'>Or</span> */}
          <Button variant='link' className='f-center ms-1 d-flex fs-9' onClick={OPEN_INTERCOM}>
            <FontAwesomeIcon type={'solid'} icon={'comments'} iconClass='me-1' />
            {content?.connect_to_support}
          </Button>
        </span>
      </div>
      {show ? <LocalizationPop {...{show, handleClose: () => setShow(false), userData}}/> : null}
    </>
  );
};

export default Customization;