import React, { Component } from 'react'
import { Form } from 'react-bootstrap';
import { connect } from 'react-redux';
import ColorPicker from '../../CommonComponents/ColorPicker';
import { getFeedlist, updateCta } from '../../../../../actions/appsMoreAction';
import Select from "react-select";
import { CARD_CTA_BTN_CLR, CARD_CTA_BTN_TEXT, CARD_CTA_BTN_URL, CARD_CTA_TEXT_CLR, IS_CARD_CTA_CHANGE } from '../../../../../reducer/keys';
import { HTMLParser, validURL } from '../../../../../utils';

class CardCTA extends Component {
    state={
        feedList:[],
        selectedFeed: [],
        urlError:false
    }

    componentWillMount(){
        const { wallData } = this.props
        getFeedlist(wallData?.ThemeInfo?.wallId || localStorage.getItem('active_wall')).then(response=>{
            const { responsedata } = response.data
            const list = responsedata?.feeds?.map(item=>({ label : HTMLParser(item.name), value : item.id }))
            const feeds = wallData?.CardStyle?.globalCTA?.feeds  ?  responsedata?.feeds?.filter(item=> wallData?.CardStyle?.globalCTA?.feeds.includes(item.id)).map(item=>({ label : item.name, value : item.id })) : []
            this.setState({feedList:list, selectedFeed:feeds})
        })
    }

    componentDidUpdate(prevProps) {
        if (this.props.save_and_publish === true && prevProps.save_and_publish !== true) {
          if (this.props.is_cta_change && !this.state.urlError) {
            setTimeout(() => {
              this.onSubmitData();
            }, 500);
          }
        }
    }

    onSubmitData = () => {
        const {wallData} = this.props
        const cta = wallData?.CardStyle?.globalCTA;
        const getFeeds = this.state.selectedFeed.map((vl) => vl.value).join(',')
  
        const formData = {
            'ctaTextColor'   : cta.color,
            'ctaButtonColor' : cta.background,
            'buttonText'     : cta.text,
            'buttonUrl'      : cta.url,
            'ctaStatus'      : cta.status,
            'feeds'          : getFeeds,
        }
        updateCta(wallData?.ThemeStyle?.id, formData, wallData?.ThemeInfo?.wallId)
        
    }

    render() {
        const { wallData, is_cta_change, changeCta, changeText, changeBtnClr, changeTextClr, changeBtnUrl, content } = this.props;
        const { feedList, selectedFeed, urlError } = this.state
        const cta = wallData?.CardStyle?.globalCTA;
        const ctaContent = content['cta']
    
        return (<>

            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="label_text">{ctaContent?.btn_text}</Form.Label>
                <Form.Control id="label_text" value={cta?.text} maxLength="25" onChange={e => changeText(e.target.value)} />
            </div>

            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="text_color">{ctaContent?.text_color}</Form.Label>
                <ColorPicker id="text_color" isDisabled={""} defaultColor={cta?.color} changeColor={(clr) => changeTextClr(clr)} colorChanged={is_cta_change} />
            </div>

            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="btn_color">{ctaContent?.btn_color}</Form.Label>
                <ColorPicker id="btn_color" isDisabled={""} defaultColor={cta?.background} changeColor={(clr) => changeBtnClr(clr)} colorChanged={is_cta_change} />
            </div>

            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="btn_url">{ctaContent?.btn_url}</Form.Label>
                <Form.Control id="btn_url" className={urlError ? 'is-invalid' : ''} value={cta?.url} onChange={e => { changeBtnUrl(e.target.value); this.setState({urlError: !validURL(e.target.value)}) }} placeholder='https://www.google.com/'/>
                {urlError && <span className='invalid-feedback'>{ctaContent?.btn_url_error}</span>}
            </div>

            {/* <div className="mt-4">
                <Form.Label className="mb-2" htmlFor={`line_trim`}>Feeds(Leave empty for all feeds) </Form.Label>
                <Select id="line_trim" className={`selectpicker selectpicker-sm`} defaultValue={selectedFeed} classNamePrefix="selectpicker" options={feedList} isMulti onChange={e=>{this.setState({selectedFeed:e});changeCta()}}/>
            </div> */}
        </>
        )
    }
}

const stateToProps = (state) => { 
    return { 

    }
}

const DispatchToProps = dispatch => {
  return {
    changeCta : () =>  dispatch({type: IS_CARD_CTA_CHANGE, payload:true}),
    changeText : (data) => dispatch({type: CARD_CTA_BTN_TEXT, payload:data}),
    changeTextClr: (data) => dispatch({type: CARD_CTA_TEXT_CLR, payload:data}),
    changeBtnClr: (data) => dispatch({type: CARD_CTA_BTN_CLR, payload:data}),
    changeBtnUrl: (data) => dispatch({type: CARD_CTA_BTN_URL, payload:data}),
  }
}

export default connect(stateToProps, DispatchToProps)(CardCTA)
