import React, { Component } from 'react'
import { Dropdown, Image, ToggleButtonGroup, ToggleButton, Button } from 'react-bootstrap';
import { baseUrl, FontAwesomeIcon } from '../../../../../Assets/AssetsHelper';
import RequestButtonHOC from '../../CommonComponents/RequestButtonHOC';
import RatingSchema from './RatingSchema';

const platforms = [{ name: 'html', value: 1 },
{ name: 'shopify', value: 2 },
{ name: 'wordpress', value: 3 },
{ name: 'square-space', value: 9 },
{ name: 'wix', value: 8 },
{ name: 'prestashop', value: 4 },
{ name: 'big-commerce', value: 5 },
{ name: 'magento', value: 6 },
{ name: 'other', value: 7 }]

export default class PlatformAside extends Component {
    render() {
        const { galleryType, changeGalleryType, changePlatform, platform, isShopify, wallData, isShoppable } = this.props
        const pageContent = window?.APP_CONTENT?.publish ?? {};
        const { galleryTypeObj = {} } = pageContent;

        const galleryTypes = [
            {
                title: galleryTypeObj?.landingPage?.dropDownTitle ?? "",
                desc: isShoppable
                    ? galleryTypeObj?.landingPage?.dropDownDescShoppable ?? ""
                    : galleryTypeObj?.landingPage?.dropDownDesc ?? "",
                icon: "browser",
                color: "#9496C8",
                value: 1,
            },
            {
                title: galleryTypeObj?.productPage?.dropDownTitle ?? "",
                desc: galleryTypeObj?.productPage?.dropDownDesc ?? "",
                icon: "box",
                color: "#7DC0AE",
                value: 2,
            },
            {
                title: galleryTypeObj?.tagBased?.dropDownTitle ?? "",
                desc: isShoppable
                    ? galleryTypeObj?.tagBased?.dropDownDescShoppable ?? ""
                    : galleryTypeObj?.tagBased?.dropDownDesc ?? "",
                icon: "image",
                color: "#FF9E9E",
                value: 3,
            },
            {
                title: galleryTypeObj?.rattingSchema?.dropDownTitle ?? "",
                desc: galleryTypeObj?.rattingSchema?.dropDownDesc ?? "",
                icon: "star",
                color: "#FDB023",
                value: 4,
            },
        ];

        return (
            <>
                <div className='p-4' id='scrollTab'>
                    <h5>{galleryTypeObj?.commonContent?.sideBardTitle ?? ""}</h5>

                    <Dropdown className="w-100" onSelect={changeGalleryType}>
                        <Dropdown.Toggle variant='secondary' className='w-100 d-flex justify-content-between align-items-center  p-2'>
                            <div className='d-flex align-items-center'>
                                <span className='f-center me-2 p-1 rounded-1 text-light w-28px' style={{ backgroundColor: galleryTypes[galleryType - 1].color }}>
                                    <FontAwesomeIcon type={'light'} icon={galleryTypes[galleryType - 1].icon} iconClass={'fs-5 me-0'} />
                                </span>
                                {galleryTypes[galleryType - 1].title}
                            </div>
                        </Dropdown.Toggle>

                        <Dropdown.Menu>
                            {galleryTypes.map((gallery, index) => (
                                (isShoppable || (index != 1)) ? <Dropdown.Item key={index} eventKey={gallery.value} className={`flex-column align-items-start w-100 mb-1 ${gallery.value == galleryType ? 'active' : ''}`}>
                                    <div className='d-flex align-items-center mb-2 text-body'>
                                        <span className='f-center me-2' style={{ color: gallery.color }}>
                                            <FontAwesomeIcon type={'light'} icon={gallery.icon} iconClass={'fs-4 me-0'} />
                                        </span>
                                        {gallery.title}
                                    </div>
                                    <p className='mb-0 fs-10 text-wrap text-gray-700'>{gallery.desc}</p>
                                </Dropdown.Item> : null
                            ))}
                        </Dropdown.Menu>
                    </Dropdown>
                </div>
                {!isShopify && galleryType != 4 ?
                    <>
                        <hr className='my-2' />
                        <div className='p-4'>
                            <h5>{galleryTypeObj?.commonContent?.sideBarPlateform ?? ""}</h5>
                            <div className="mb-3">
                                <ToggleButtonGroup className='flex-wrap justify-content-between d-flex platform-selection' type="radio" name="platform" value={platform} defaultValue={platform} onChange={changePlatform}>
                                    {platforms.map((s_platform, index) => (
                                        <ToggleButton key={index} id={`s_platform-${s_platform.value}`} value={s_platform.value} variant="secondary" className={`mb-3 py-3 me-0 ${s_platform.value == platform ? 'bg-body' : ''}`}>
                                            <Image loading='lazy' data-mode="light" src={baseUrl(`media/icons/cms-platform/${s_platform.name}.svg`)} alt={s_platform.name} title={s_platform.name} width={180} fluid />
                                            {/* height={26}
                                            className="h-26px" */}
                                            <Image loading='lazy' data-mode="dark" src={baseUrl(`media/icons/cms-platform/${s_platform.name}-light.svg`)} alt={s_platform.name} title={s_platform.name} width={280} fluid />
                                        </ToggleButton>
                                    ))}
                                    <RequestButtonHOC title={galleryTypeObj?.modal?.req_platform?.title ?? ""}>
                                        <Button variant="secondary" size="lg" className={`w-100 p-3 flex-column`}>
                                            <FontAwesomeIcon icon={'plus'} marginRight={0} />
                                            {galleryTypeObj?.modal?.req_platform?.title ?? ""}
                                        </Button>
                                    </RequestButtonHOC>
                                </ToggleButtonGroup>
                            </div>
                        </div>
                    </>
                    : null}

                {
                    galleryType == 4 ? <>
                        <hr className='my-2' />
                        <RatingSchema />
                    </> : null
                }
            </>
        )
    }
}
