import React, { Component } from 'react'
import AttachImage from '../../CommonComponents/Uploads/AttachImage';
import { CHANGE_BANNER } from '../../../../../reducer/keys';
import { connect } from 'react-redux';
import { Form } from "react-bootstrap";

class CustomHeader extends Component {
    updateBanner = (key, key2, key3, value) => {
        const { wall, changeBanner } = this.props;
        const updatedData = wall?.Banner?.map(item => ({ ...item, [key]: {...item[key], [key2]: key3 ? {...item[key][key2], [key3]: value} : value }, userUpdated: 1 }));
       changeBanner({updatedData})
    }
    render() {
        const { wall } = this.props;
        const data = wall?.Banner?.[0];
        return (
            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="s_pi">Background Image</Form.Label>
                <AttachImage id="s_pi" imageStyle={{ paddingBottom: '66%' }} backgroundImage={data?.background?.url} onClickTouploadImage={(url) => this.updateBanner('background', 'url', null, url)} />
            </div>
        )
    }
}

const mapDispatchToProps = dispatch => {
    return {
        changeBanner: (data) => dispatch({type:CHANGE_BANNER, payload:data})
    }
}

export default connect(null, mapDispatchToProps)(CustomHeader)
