import React, { Component } from 'react'
import { Form } from 'react-bootstrap'
import { connect } from 'react-redux'
import ColorPicker from '../../CommonComponents/ColorPicker'
import { HASH_ALL, HASH_COLOR, HASH_FEED, HASH_STATUS } from '../../../../../reducer/keys'
import { hashHighlight } from '../../../../../actions/appsMoreAction'

class HashHighlight extends Component {

    componentDidUpdate(prevProps) {
        if (this.props.save_and_publish === true && prevProps.save_and_publish !== true) {
          if (this.props.is_hash_changed) {
            setTimeout(() => {
              this.onSubmitData();
            }, 500);
          }
        }
    }

    onSubmitData = () => {
        const { wall } = this.props
        const { hashtag } = wall?.CardStyle
        const data = {
            hashtag_color: hashtag?.color || '#000000',
            hashtag_feed: hashtag?.feedHashtag || 0,
            hashtag_all: hashtag?.allHashtag || 0,
            hashtag_highlight: hashtag?.colorStatus || 0,
            website_id: localStorage.getItem('website_id'),
            wallId: localStorage.getItem('active_wall')
        };

        hashHighlight(data)
    }

    render() {
        const { wall, is_hash_changed, hashColor, hashAll, hashFeed, hashStatus, content } = this.props
        const { hashtag } = wall?.CardStyle
        return <>
            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="hash_color">{content?.color}</Form.Label>
                <ColorPicker id="hash_color" defaultColor={hashtag?.color} changeColor={(clr) => hashColor(clr) } colorChanged={is_hash_changed} />
            </div>

            <div className="mt-4">
                <Form.Check type="switch" id="hash_feed" className="f-between" label={content?.feed_hashtag} checked={hashtag?.feedHashtag} onChange={(e) => hashFeed(Number(e.target.checked))} reverse />
            </div>

            <div className="mt-4">
                <Form.Check type="switch" id="hash_tag" className="f-between" label={content?.all_hashtag} checked={hashtag?.allHashtag} onChange={(e) => hashAll(Number(e.target.checked))} reverse />
            </div>

            <div className="mt-4">
                <Form.Check type="switch" id="hash_status" className="f-between" label={content?.custom_css_status} checked={hashtag?.colorStatus} onChange={(e) => hashStatus(Number(e.target.checked))} reverse />
            </div>
        </>
    }
}

const stateToProps = (state) => { 
    return { 
        is_hash_changed: state.appData?.is_hash_changed,
    }
  }

const DispatchToProps = dispatch => {
    return {
        hashColor: (data) => dispatch({type: HASH_COLOR, payload: data}),
        hashStatus: (data) => dispatch({type: HASH_STATUS, payload:data}),
        hashAll: (data) => dispatch({type: HASH_ALL, payload:data}),
        hashFeed: (data) => dispatch({type: HASH_FEED, payload:data})
    }
}

export default connect(stateToProps, DispatchToProps)(HashHighlight)
