import React, { Component } from 'react'
import Select from 'react-select';
import ErrorFound from '../../../../../ErrorFound';
import { shopifyApp } from '../../../../../../utils';
import { Form, Row, Col } from 'react-bootstrap';
import RangeInput from '../../../CommonComponents/RangeInput';
import InheritSwitch from '../../Customization/Element/InheritSwitch';
import ColorPicker from '../../../CommonComponents/ColorPicker';
import SelectFont from '../../Components/SelectFont';


class ProductButton extends Component {

    styles = {
        option: (provided, state) => ({ ...provided, fontFamily: state.data.font }),
        singleValue: (provided, state) => ({ ...provided, fontFamily: state.data.font })
    };

    render() {
        const { onChangeButton_text, onChangeButton_font, onChangeButton_verient, onChangeButton_fontSize,  onChange_tab_target, onChange_pdp_target, inheritStyle, onChangeButton_color, onChangeButton_background, ProdButton, is_cta_setting_change, content } = this.props
        const options = content?.product_cta?.click_redirect
        return (
            <>
            <ErrorFound><InheritSwitch content={content} /></ErrorFound>
            <div className="mt-4">
                <Form.Label className="mb-2 d-flex flex-wrap" htmlFor={`btn_text`}>{content?.text}</Form.Label>
                <Form.Control size='sm' placeholder="Featured Product" id="btn_text" defaultValue={ProdButton?.text} onChange={onChangeButton_text} />
            </div>
            <div className="mt-4">
                <SelectFont inheritStyle={inheritStyle} fontChange={onChangeButton_font} fontval={ProdButton?.font} variantChange={onChangeButton_verient} vrntVal={ProdButton?.varient} content={content}/>
            </div>
            <div className="mt-4">
                <Form.Label className="mb-2 d-flex flex-wrap" htmlFor={`shop_btn_color`}>{content?.font_color}</Form.Label>
                <ColorPicker id="shop_btn_color" defaultColor={ProdButton?.color} changeColor={onChangeButton_color} colorChanged={is_cta_setting_change}/>
            </div>
            <div className="mt-4">
                <Form.Label className="mb-2 d-flex flex-wrap" htmlFor={`shop_btn_bg`}>{content?.background_color}</Form.Label>
                <ColorPicker id="shop_btn_bg" defaultColor={ProdButton?.background} changeColor={onChangeButton_background} colorChanged={is_cta_setting_change}/>
            </div>
            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="shop_btn_size">{content?.font_size}</Form.Label>
                <RangeInput id="shop_btn_size" min={10} max={25} step={1} value={ProdButton?.size} onChange={onChangeButton_fontSize}/>
            </div>
            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="on_btn_click">{content?.more_actions?.on_click}</Form.Label>
                <Select className={`selectpicker selectpicker-sm`} classNamePrefix="selectpicker" options={options} onChange={onChange_tab_target} value={options.find(i => parseInt(i.value) == parseInt(ProdButton?.newTab))}/> 
            </div>
            {shopifyApp() ?
            <div className="mt-4">
                <Form.Check type="switch" id="redirect_pdp" className="f-between" label="Redirect to PDP(Product Page)" defaultValue={ProdButton?.pdp_target} checked={ProdButton?.pdp_target} onChange={onChange_pdp_target} reverse/>
            </div> : null}
        </>
        )
    }
}

export default ProductButton;