import React, { Component } from 'react'
import { Row, Col, Tab, Tabs, OverlayTrigger, Button, Tooltip } from 'react-bootstrap';
import EmbedCode from '../Components/EmbedCode';
import { COPY_TO_CLIPBOARD, formatTemplateText, SWAL_FIRE } from '../../../../../../utils';
import { baseUrl, FontAwesomeIcon } from '../../../../../../Assets/AssetsHelper';
import AutomaticPublish from './AutomaticPublish';
import { connect } from 'react-redux';
import { DELETE_SHOPIFY_PUBLISHED, GET_SHOPIFY_PUBLISH_DATA, PUBLISH_SHOPIFY_GALLERY } from '../../../../../../actions/shopifyAction';
import TagBase from '../Components/TagBase';
import { COMPANY_NAME } from '../../../../../../actions/api';

export const NumberBullet = ({ count }) => <span className='d-inline-block text-center h-20px w-20px rounded-circle me-2 bg-primary text-gray-100 fs-9 flex-shrink-0 flex-shrink-0' style={{ lineHeight: '20px' }}>{count}</span>

class ShopifyPublish extends Component {
       state = { theme: "", page: "", section: "", position: { label: 'Top', value: '0' }, pageData: [], themeSection: false, copied: false, defaultActiveKey: 'theme_ex', positions: [{ label: 'Top', value: '0' }, { label: 'Bottom', value: '1' }], dataTag: false }
       copyText = (id) => {
              COPY_TO_CLIPBOARD(id)
              this.setState({ copied: true })
              setTimeout(() => { this.setState({ copied: false }) }, 1000)
       }

       componentDidMount() {
              const { ShopifyData, galleryType } = this.props
              if (ShopifyData && ShopifyData?.storeThemes && ShopifyData?.pages) this.setShopifyData()
              else this.props.GET_SHOPIFY_PUBLISH_DATA(galleryType == 2 ? true : false)
       }

       componentDidUpdate(prevProps, prevState) {
              if (this.props.ShopifyData !== prevProps.ShopifyData) {
                     const { ShopifyData } = this.props
                     if (ShopifyData && ShopifyData?.storeThemes && ShopifyData?.pages) this.setShopifyData()
              }
              if (this.props.galleryType != prevProps.galleryType) {
                     this.setState({ defaultActiveKey: 'theme_ex' })
                     this.props.GET_SHOPIFY_PUBLISH_DATA(this.props.galleryType == 2 ? true : false)
              }
       }


       setShopifyData = () => {
              const { ShopifyData } = this.props
              let storeThemes = []
              let Pages = []
              let themeList = []

              Object.entries(ShopifyData.storeThemes.length ? ShopifyData.storeThemes[0] : '')?.map(([key, value]) => (storeThemes.push({ label: key, value: `${value.dawn},${value.id}` })))

              ShopifyData.themeSection.length && ShopifyData.themeSection.map(theme => themeList.push({ value: theme.section, label: theme.title }))

              Object.entries(ShopifyData?.pages)?.map(([key, value]) => (Pages.push({ label: key, value: `${key},${value}` })))

              this.setState({
                     pageData: Pages,
                     theme: storeThemes?.[0]?.value ?? '',
                     page: Pages?.[0] ?? '',
                     section: themeList?.[0] ?? '',
                     themeSection: themeList,
                     position: this.state.positions[0]
              });
       }

       onThemeChange = (e) => this.setState({ theme: e.value })
       onSectionChange = (e) => this.setState({ section: e })
       onPositionChange = (e) => this.setState({ position: e })


       onChangePages = (e) => {
              if (e.value.includes('HomePage')) this.setState({ page: e })
              else this.setState({ page: e })
       }

       publishGallery = (callBack) => {
              const { theme, page, section, position } = this.state
              const { galleryType, PUBLISH_SHOPIFY_GALLERY } = this.props
              var obj = {
                     wallId: localStorage.getItem('website_id'),
                     theme: theme,
                     page: page.value,
                     section: section.value,
                     position: String(position.label).toLowerCase()
              }
              PUBLISH_SHOPIFY_GALLERY(obj, callBack, galleryType == 2 ? true : false)
       }

       deleteGallery = (id) => {
              const { publishOption } = this.props
              const pageContent = window?.APP_CONTENT?.publish?.galleryTypeObj ?? {};
              const { shopifyPublish } = pageContent;

              SWAL_FIRE({
                     title: shopifyPublish?.are_you_sure,
                     icon: "warning",
                     showCancelButton: true,
                     confirmButtonText: shopifyPublish?.yes,
                     cancelButtonText: shopifyPublish?.no,
              }).then((result) => {
                     if (result.value) {
                            var obj = new FormData()
                            obj.append("id", id)
                            this.props.DELETE_SHOPIFY_PUBLISHED(id, publishOption)
                     }
                     else if (result.dismiss === "cancel") { }
              });

       }


       gotoCustomize = () => {
              const { userData, ShopifyData } = this.props;
              const firstObject = ShopifyData?.storeThemes[0];
              const firstKey = Object.keys(firstObject)[0];
              const firstThemeId = firstObject[firstKey].id;
              this.copyText('web_id_');
              if (userData?.shopifyShopDetails?.shopUrl && firstThemeId) {
                     const url = `https://${userData.shopifyShopDetails.shopUrl}/admin/themes/${firstThemeId}/editor`;
                     window.open(url, '_blank'); // Open in a new tab
              }
       }

       render() {
              const { ShopifyData, galleryType, allTags, loader } = this.props
              const { pageData, themeSection, copied, page, section, positions, position, dataTag, defaultActiveKey } = this.state
              const pageContent = window?.APP_CONTENT?.publish?.galleryTypeObj ?? {};
              const { shopifyPublish } = pageContent ?? {};

              return <div className='afterPublish_screen'>
                     {galleryType != 3 ? <Tabs activeKey={defaultActiveKey} onSelect={event => this.setState({ defaultActiveKey: event })} id="justify-tab-example" className="mb-3">
                            {galleryType == 1 ? <Tab eventKey="theme_ex" title={shopifyPublish?.tab_heading}>
                                   <Row>
                                          <Col>
                                                 <h5 className="mb-3">{shopifyPublish?.tab_heading}</h5>
                                                 <div className='px-lg-3'>

                                                        <div className='d-flex align-items-center justify-content-start ps-8 mb-5'>
                                                               <span className='fw-bold'>{shopifyPublish?.websiteId} : </span>
                                                               <div className="position-relative embed_code embed_code_box iframe_embcode_box rounded-3 ms-2">
                                                                      <code className="form-control fs-9 pe-10 fw-bold" id="web_id_" onClick={() => { this.copyText('web_id_') }}>
                                                                             {localStorage.getItem('website_id')}
                                                                      </code>
                                                                      <div className="position-absolute end-0 top-0 me-2 h-100 f-center">
                                                                             <OverlayTrigger placement="top" overlay={<Tooltip>{!copied ? shopifyPublish?.copy_code : shopifyPublish?.copied_code}</Tooltip>}>
                                                                                    <Button variant="link" className="text-gray-300" onClick={() => this.copyText('web_id_')}>
                                                                                           {!copied ?
                                                                                                  <FontAwesomeIcon icon={'copy'} />
                                                                                                  :
                                                                                                  <FontAwesomeIcon icon="check" height={16} width={16} iconClass="text-success" />
                                                                                           }
                                                                                    </Button>
                                                                             </OverlayTrigger>
                                                                      </div>
                                                               </div>
                                                               <button type="button" className="ms-3 btn btn-primary" onClick={() => this.gotoCustomize()} ><i className="fas fa-external-link-alt" ></i>{shopifyPublish?.go_customize}</button>
                                                        </div>


                                                        {Object.values(shopifyPublish?.list).map((text, index) => (
                                                               <p className={`h6 mb-4 ${![7, 8].includes(index) ? 'd-flex' : ''}`}> {index != 8 ? <NumberBullet count={index + 1} /> : null} {formatTemplateText(text, { COMPANY_NAME })} </p>
                                                        ))}
                                                 </div>
                                          </Col>
                                          {/* <Col>
                                                 <div className='d-flex justify-content-end'>
                                                        <video className='w-100' width="320" height="500" poster={baseUrl('media/video/shopify-extension.png')} controls={false} autoPlay={true} loop={true}>
                                                               <source src={baseUrl('media/video/shopify-extension.mp4')} type="video/mp4" />
                                                        </video>
                                                 </div>
                                          </Col> */}
                                   </Row>
                            </Tab> : null}
                            <Tab eventKey="automatic_publishing" title={shopifyPublish?.auto_publish}>
                                   <AutomaticPublish galleryType={galleryType} ShopifyData={ShopifyData} pageData={pageData} page={page} onChangePages={this.onChangePages} onSectionChange={this.onSectionChange} onPositionChange={this.onPositionChange} section={section} themeSection={themeSection} positions={positions} position={position} publishGallery={this.publishGallery} deleteGallery={this.deleteGallery} loader={loader} />
                            </Tab>
                            <Tab eventKey="manual_publish" title={shopifyPublish?.menual_publishing}>
                                   {galleryType == 3 ? <TagBase allTags={allTags} count={1} updateData={(val) => this.setState({ dataTag: val })} /> : null}
                                   <div className='px-lg-3'>
                                          <EmbedCode platform={2} galleryType={galleryType} dataTag={dataTag} allTags={allTags} count={galleryType == 3 ? 2 : 1} />
                                   </div>
                            </Tab>
                     </Tabs> : null}

                     {galleryType == 3 ? <>
                            <TagBase allTags={allTags} count={1} updateData={(val) => this.setState({ dataTag: val })} />
                            <div className='px-lg-3'>
                                   <EmbedCode platform={2} galleryType={galleryType} dataTag={dataTag} allTags={allTags} count={galleryType == 3 ? 2 : 1} />
                            </div>
                     </> : null}
              </div>

       }
}

const mapStateToProps = (state) => {
       return {
              ShopifyData: state.ShopifyData?.ShopifyData,
              activeWebsite: state.websiteList?.activeWebsite_id,
              allTags: state.allTags?.selectedTags,
              loader: state.ShopifyData?.isRequesting,
              userData: state.authorized?.userData?.user_data,
       }
}
const mapDispatchToProps = dispatch => {
       return {
              PUBLISH_SHOPIFY_GALLERY: (obj, callback, status) => dispatch(PUBLISH_SHOPIFY_GALLERY(obj, callback, status)),
              DELETE_SHOPIFY_PUBLISHED: (obj) => dispatch(DELETE_SHOPIFY_PUBLISHED(obj)),
              GET_SHOPIFY_PUBLISH_DATA: (isProduct) => dispatch(GET_SHOPIFY_PUBLISH_DATA(isProduct))
       }
}
export default connect(mapStateToProps, mapDispatchToProps)(ShopifyPublish);