import React, { Component } from 'react'
import PublishSteps from './Components/PublishSteps'
import { connect } from 'react-redux'
import { FontAwesomeIcon } from '../../../../../Assets/AssetsHelper';
import { Card, Button } from 'react-bootstrap';
import { OPEN_INTERCOM } from '../../../../../utils';
import RatingSteps from './Components/RatingSteps';


class GenerateCode extends Component {
    state = { homePopup: false, productPopup: false, tagPopup: false, mainContainer: true, emailPopup: false }
    render() {
        const { allTags, platform, closePopup, isShopify, isBigCommerce, galleryType, wallData, isShoppable } = this.props
        const pageContent = window?.APP_CONTENT?.publish ?? {};
        const { galleryTypeObj = {} } = pageContent;
        
        return (<>
            {galleryType != 4 ? <PublishSteps content={galleryTypeObj} platform={platform} galleryType={galleryType} allTags={allTags} closePopup={closePopup} isShopify={isShopify} isBigCommerce={isBigCommerce} isShoppable={isShoppable} /> : <RatingSteps wallData={wallData} />}
            <Card.Footer className="f-center justify-content-start position-absolute bottom-0 start-0 w-100 bg-body" style={{ height: 66 }}>
                <div className="py-1 px-3 rounded-pill badge-light-primary text-nowrap d-flex flex-wrap align-items-center">
                    <p className="my-1 text-muted d-md-inline d-none">{galleryTypeObj?.commonContent?.connectSupport ?? ""}</p>
                    <p className="my-1 text-muted d-md-none d-inline">Help</p>
                    <FontAwesomeIcon icon={'angle-right'} iconClass="mx-2" />
                    <Button variant='link' className='f-center d-flex' onClick={OPEN_INTERCOM}>
                        <FontAwesomeIcon type={'solid'} icon={'comments'} iconClass='me-1' />
                        {galleryTypeObj?.commonContent?.connectSupportLink ?? ""}
                    </Button>
                </div>
            </Card.Footer>
        </>
        )
    }
}
const mapStateToProps = state => {
    const { allTags } = state
    return {
        allTags: allTags?.selectedTags
    }
}


export default connect(mapStateToProps)(GenerateCode)