import React, { Component, memo } from 'react';
import { WEBSITE_EMBED_URL } from '../../../../../actions/api';
import DemoAlert from '../Components/DemoAlert'
import { shopifyApp } from '../../../../../utils';

class Theme extends Component {

    embedCode = () => {

        const { ownerId } = this.props;
        window.isTagShop = window.preview = true;
    
        const container = document.querySelector('.tagshop-socialwall');
        if (!container?.getAttribute("data-is-load") || container.getAttribute("data-is-load") == "0") {
            fetch(`${WEBSITE_EMBED_URL}${ownerId == 139294 ? 'khaadi/' : ''}embed.json`)
                .then(res => res.json())
                .then(({ css, script }) => {
                    document.head.appendChild(Object.assign(document.createElement("link"), { type: "text/css", rel: "stylesheet", href: css }));
                    document.body.appendChild(Object.assign(document.createElement("script"), { src: script }));
                });
        }
    }
    
    componentWillUnmount() {
        this.onLoadRemoveScript()
        this.onLoadRemoveCss()
    }

    removeElementById = (selector, id) => {
        try {
            document.querySelector(`${selector}#${id}`)?.remove();
        } catch (ex) {}
    };
    
    onLoadRemoveCss = () => this.removeElementById("link[type='text/css']", "preview_embed_css");
    onLoadRemoveScript = () => this.removeElementById("script[type='text/javascript']", "preview_embed_js");

    render() {
        const { webId, isDemo, themeId,  appData } = this.props
        const bnrStatus = appData?.activeBanner ?? false;
        const reviewStatus = appData?.wall?.SnapUp?.QrCode?.status
        const websiteId = localStorage.getItem("website_id") || webId;
        
        return <>
            {isDemo == 1 && <DemoAlert />}
            <div className={`theme_preview ${shopifyApp() ? 'shopify-theme-preview': ''}`} data-theme-id={themeId}
            data-banner-status={bnrStatus} data-review-status={reviewStatus}>
                <iframe title='theme_live_preview' key={websiteId} src={`${WEBSITE_EMBED_URL}${websiteId}?website=1&editor=1`} id="websiteLivePreview"></iframe>
            </div>
        </>
    }
}

export default memo(Theme);