import { Component } from "react";
import ErrorFound from "../../../../ErrorFound";
import InheritSwitch from "./Element/InheritSwitch";
import SelectFont from "../Components/SelectFont";
import { Form } from "react-bootstrap";
import ColorPicker from "../../CommonComponents/ColorPicker";
import RangeInput from "../../CommonComponents/RangeInput";
import { connect } from "react-redux";
import { BANNER_HEADER_CHANGE, CHANGE_BANNER} from "../../../../../reducer/keys";

class ReviewHeader extends Component{

    updateBanner = (key, key2, key3, value, status = false) => {
        const { wallData, changeBanner, changeBannerHeader } = this.props;
        const data = wallData?.Banner?.[0];
        if(!status) if(String(value)?.trim() == (key3 ? String(data[key][key2][key3])?.trim() : String(data[key][key2])?.trim())) return false;
        const updatedData = wallData?.Banner?.map(item => ({ ...item, [key]: { ...item[key], [key2]: key3 ? { ...item[key][key2], [key3]: value, [key3 == 'font' && 'fontLink']: key3 == 'font' && value?.replace(/ /g, '+') } : value }, userUpdated: 1, wallId: localStorage.getItem('active_wall'), website_id: localStorage.getItem('website_id') }));
        
        changeBanner({ updatedData });
        changeBannerHeader(true);
    }

    render(){
        const { inheritStyle, wallData, content } = this.props;
        const { Banner} = wallData
        const data = Banner?.[0]
        const reviewTest = content['banner_style']
        
        return <>
            <ErrorFound><InheritSwitch content={content}/></ErrorFound>
            <div>
                <p className="h5">{reviewTest?.heading}</p>
            </div>
            {/* <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="h_t">Header text</Form.Label>
                <Form.Control id="h_t" value={data?.title.text} maxLength="30" onChange={e => {this.updateBanner('title', 'text', 0, e.target.value)}} />
            </div> */}
            <div className="mt-4">
                <SelectFont inheritStyle={inheritStyle} fontChange={(e) =>{this.updateBanner('title', 'style', 'font', e.label.key)}} variantChange={(e) => {this.updateBanner('title', 'style', 'variant', e.label.key)}} fontval={data?.title.style.font} vrntVal={data?.title.style.variant} content={content} />
            </div>
            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="h_c">{content?.font_color}</Form.Label>
                <ColorPicker id="h_c" isDisabled={inheritStyle ? "disabled" : ""} defaultColor={data?.title.style.color} changeColor={(clr) => {this.updateBanner('title', 'style', 'color', clr)}} inheritStyle={inheritStyle} />
            </div>
            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="h_s">{reviewTest?.font_size}</Form.Label>
                <RangeInput inheritStyle={inheritStyle} id="h_s" min={10} max={50} step={1} defaultValue={data?.title.style.size} value={data?.title.style.size} onChange={(event) => { this.updateBanner('title', 'style', 'size', Number(event)) }} />
            </div>
            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="bg_c">{reviewTest?.background_color}</Form.Label>
                <ColorPicker id="bg_c" isDisabled={""} defaultColor={data?.background?.primaryColor} changeColor={(clr) => {this.updateBanner('background','primaryColor',null, clr)}} />
            </div>
            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="s_c">{reviewTest?.star_color}</Form.Label>
                <ColorPicker id="s_c" isDisabled={inheritStyle ? "disabled" : ""} defaultColor={data?.star?.color} changeColor={(clr) => {this.updateBanner('star','color',null, clr)}} inheritStyle={inheritStyle} />
            </div>
            <hr className=""></hr>
            <div>
                <p className="h5">{reviewTest?.review_button}</p>
            </div>
            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="b_t">{reviewTest?.button_text}</Form.Label>
                <Form.Control id="b_t" value={data?.button?.text} maxLength="30" onChange={e => { this.updateBanner('button','text',null, e.target.value, true) }} />
            </div>
            <div className="mt-4">
                <SelectFont inheritStyle={inheritStyle} fontChange={event=> {this.updateBanner('button','style','font', event.label.key)}} variantChange={event=> {this.updateBanner('button','style','variant', event.label.key)}} fontval={data?.button?.style?.font} vrntVal={data?.button?.style?.variant} content={content} />
            </div>
            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="b_c">{content?.font_color}</Form.Label>
                <ColorPicker id="b_c" isDisabled={inheritStyle ? "disabled" : ""} defaultColor={data?.button?.style?.color} changeColor={(clr) => {this.updateBanner('button','style','color', clr)}} inheritStyle={inheritStyle} />
            </div>
            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="b_bgc">{reviewTest?.background_color}</Form.Label>
                <ColorPicker id="b_bgc" isDisabled={inheritStyle ? "disabled" : ""} defaultColor={data?.button?.style?.background} changeColor={(clr) => {this.updateBanner('button','style','background', clr)}} inheritStyle={inheritStyle} />
            </div>
            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="b_s">{reviewTest?.font_size}</Form.Label>
                <RangeInput inheritStyle={inheritStyle} id="b_s" min={10} max={50} step={1} defaultValue={ data?.button?.style?.size} value={data?.button?.style?.size} onChange={(event) => { this.updateBanner('button','style','size', Number(event)) }} />
            </div>
        </>
    }
}

const mapStateToProps = (state) => {
    return {
        allFont: state.allFont?.allFonts,
        isHeaderUpdate: state.appData?.isHeaderUpdate,
        inheritStyle: state.appData?.wall?.CardStyle?.inheritStyles,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        changeBanner: (data) => dispatch({type:CHANGE_BANNER, payload:data}),
        changeBannerHeader: (data) => dispatch({ type: BANNER_HEADER_CHANGE, payload: data })
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ReviewHeader)